<template>
  <div :class="computedClasses">
    <label class="label">
      <template v-if="tagName !== 'textarea'">
        <the-mask
          v-if="mask"
          :mask="mask"
          v-on="listeners"
          v-bind="attrs"
          @focus.native="$emit('focus')"/>

        <input
          v-else
          v-on="listeners"
          v-bind="attrs">
      </template>

      <textarea
        v-else
        v-on="listeners"
        v-bind="attrs"></textarea>

      <span v-if="animatedPlaceholder && placeholder" class="placeholder">
        <span class="placeholder-text">
          <span>{{ placeholder }}</span>
          <span v-if="required" class="asterisk"> *</span>
        </span>
      </span>

      <a
        href="#"
        v-if="type === 'password'"
        class="field-icon"
        @click.stop.prevent="changePasswordType">
        <svg-icon :icon="(localType === 'password') ? 'eye' : 'eye-close'" :size="24"/>
      </a>

      <a
        href="#"
        v-if="clearIcon && type !== 'password' && hasValue"
        class="field-icon"
        @click.stop.prevent="clearValue">
        <svg-icon icon="cross" :size="12"/>
      </a>

      <a
        href="#"
        v-else-if="hasIconSlot && type !== 'password' && !loading"
        class="field-icon"
        @click.stop.prevent="iconClick($event)">
        <slot name="icon"></slot>
      </a>

      <span
        v-if="loading"
        class="field-icon">
        <svg-icon icon="preloader" spin :size="20"/>
      </span>
    </label>

    <div v-if="hasNoteSlot" class="note">
      <slot name="note"></slot>
    </div>

    <div v-if="hasErrorSlot" class="error">
      <slot name="error"></slot>
    </div>
  </div>
</template>

<script>
import { TheMask } from 'vue-the-mask';

export default {
  name: 'TextField',
  components: {
    TheMask,
  },
  props: {
    tagName: {
      type: String,
      default: 'input',
      validator: (value) => ['input', 'textarea'].indexOf(value) !== -1,
    },
    type: {
      type: String,
      default: 'text',
    },
    rows: {
      type: Number,
      default: 1,
    },
    value: {
      type: String,
      default: '',
    },
    baseClassName: {
      type: String,
      default: 'text-field',
    },
    size: {
      type: String,
      default: '',
    },
    color: {
      type: String,
      default: '',
    },
    placeholder: {
      type: String,
      default: null,
    },
    animatedPlaceholder: {
      type: Boolean,
      default: true,
    },
    mask: {
      type: String,
      default: null,
    },
    clearIcon: {
      type: Boolean,
      default: false,
    },
    readonly: {
      type: Boolean,
      default: false,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    autofocus: {
      type: Boolean,
      default: false,
    },
    required: {
      type: Boolean,
      default: false,
    },
    noBorders: {
      type: Boolean,
      default: false,
    },
    autoHeight: {
      type: Boolean,
      default: false,
    },
    loading: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    computedClasses () {
      return [
        this.baseClassName,
        this.colorModifier,
        this.sizeModifier,
        this.disabledModifier,
        this.noBordersModifier,
        this.hasValueModifier,
        this.errorModifier,
        this.iconModifier,
      ];
    },
    hasValue () {
      return this.localValue !== '';
    },
    colorModifier () {
      return (this.color !== '') ? `-${this.color}` : '';
    },
    sizeModifier () {
      return (this.size !== '') ? `-${this.size}` : '';
    },
    hasValueModifier () {
      return this.hasValue ? '-has-value' : '';
    },
    disabledModifier () {
      return (this.disabled) ? '-disabled' : '';
    },
    noBordersModifier () {
      return this.noBorders ? '-no-borders' : '';
    },
    hasNoteSlot () {
      return !!this.$slots.note;
    },
    hasErrorSlot () {
      return !!this.$slots.error;
    },
    hasIconSlot () {
      return !!this.$slots.icon;
    },
    errorModifier () {
      return this.hasErrorSlot ? '-has-error' : '';
    },
    hasLocalIcon () {
      return this.type === 'password' || (this.clearIcon && this.hasValue) || this.loading;
    },
    iconModifier () {
      return (
        this.hasIconSlot
        || this.hasLocalIcon
        || (this.clearIcon && this.hasValue)) ? '-has-icon' : '';
    },
    listeners () {
      return {
        ...this.$listeners,
        input: (event) => {
          const value = (this.mask) ? event : event.target.value;
          this.$emit('input', value, event);

          if (this.autoHeight && this.tagName === 'textarea') {
            event.target.style.height = 'auto';
            event.target.style.height = `${event.target.scrollHeight}px`;
          }
        },
        change: (event) => {
          const value = (this.mask) ? event : event.target.value;
          this.localValue = value;
          this.$emit('change', value, event);
        },
        blur: (event) => {
          const value = (this.mask) ? event : event.target.value;
          this.localValue = value;
          this.$emit('blur', value, event);
        },
      };
    },
    attrs () {
      return {
        ...this.$attrs,
        value: this.localValue,
        type: (this.tagName !== 'textarea') ? this.localType : null,
        rows: (this.tagName === 'textarea') ? this.rows : null,
        placeholder: !this.animatedPlaceholder ? this.placeholder : null,
        readonly: this.readonly,
        disabled: this.disabled,
        autofocus: this.autofocus,
        ref: 'field',
        autocomplete: 'off',
        class: 'field',
      };
    },
  },
  data: () => ({
    localValue: '',
    localType: '',
  }),
  watch: {
    value: {
      immediate: true,
      handler (val) {
        this.localValue = val;
      },
    },
    type: {
      immediate: true,
      handler (val) {
        this.localType = val;
      },
    },
  },
  methods: {
    iconClick (event) {
      if (!this.disabled && !this.loading) {
        this.$emit('icon-click', event);
      }
    },
    changePasswordType () {
      this.localType = (this.localType === 'password') ? 'text' : 'password';

      this.$emit('change-password-visibility', this.localType);
    },
    clearValue () {
      this.$emit('input', '');
      this.$emit('cleared');
    },
    focus () {
      this.$el.querySelector(this.tagName).focus();
    },
  },
};
</script>

<style lang="scss" scoped>
$border-radius: 6px;
$input: (
  font-family: $global-font-family,
  font-style: normal,
  font-weight: normal,
  font-size: 16px,

  placeholder-font-size-active: 12px,
  note-font-size: 12px,

  padding: (10px, 16px),
  height: 40px,
  placeholder-padding: (0, 12px),
);
$sizes: (
  small: (
    padding: (8px, 16px),
    height: 32px,
    font-size: 14px,
    placeholder-padding: (0, 12px),
    placeholder-offset: 8px,
    placeholder-offset-active: -15px,
  )
);

.text-field {
  display: block;
  position: relative;
  width: 100%;
  font-family: map-get($input, font-family);
  font-style: map-get($input, font-style);
  font-weight: map-get($input, font-weight);
  font-size: map-get($input, font-size);

  .label {
    position: relative;
    display: flex;
    width: 100%;
  }

  .field {
    display: block;
    width: 100%;
    min-width: 0;
    border: none;
    box-shadow: inset 0 0 0 1px $gray-60;
    border-radius: $border-radius;
    color: $gray-90;
    font-family: inherit;
    background-color: $white;
    text-overflow: ellipsis;
    padding: nth(map-get($input, padding), 1) nth(map-get($input, padding), 2);
    font-size: map-get($input, font-size);
    height: map-get($input, height);

    &:focus {
      outline: none;
      box-shadow: inset 0 0 0 2px $gray-70;
    }

    @include placeholder{
      color: $gray-60;
      font-family: inherit;
      font-size: inherit;
      line-height: 1;
    }
  }

  textarea.field {
    resize: none;
    height: auto;
    max-height: 160px;
    min-height: map-get($input, height);
    @include scrollbar(4px, 6px, $gray-30, transparent);
  }

  .placeholder {
    display: block;
    position: absolute;
    top: 13px;
    left: 0;
    max-width: 100%;
    font-size: inherit;
    line-height: 1;
    padding:
      nth(map-get($input, placeholder-padding), 1)
      nth(map-get($input, placeholder-padding), 2);
    color: $gray-60;
    transition: transform .2s, font-size .2s, color .2s;
    user-select: none;
    z-index: 2;

    &-text {
      position: relative;
      padding: 0 4px;
      background-color: $white;
      border-radius: 2px;
      vertical-align: top;
      @include truncate(100%);
    }

    .asterisk {
      color: $red-50;
    }
  }

  .field-icon {
    position: absolute;
    top: 0;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    z-index: 2;
    color: $gray-60;
    width: map-get($input, height);
    border: none;
    text-decoration: none;

    &:hover {
      color: $gray-90;
    }
  }

  .note,
  .error {
    margin-top: 5px;
    font-size: map-get($input, note-font-size);
    padding: 0 nth(map-get($input, padding), 2);
  }

  .note {
    color: $gray-80;
  }

  .error {
    color: $red-50;
  }

  &.-has-icon .field {
    padding-right: map-get($input, height);
  }

  .field:focus + .placeholder,
  &.-has-value .placeholder {
    color: $gray-80;
      font-size: map-get($input, placeholder-font-size-active);
    transform: translateY(-19px);
  }

  @each $name, $options in $sizes {
    &.-#{$name} {
      .field {
        padding: nth(map-get($options, padding), 1) nth(map-get($options, padding), 2);
        font-size: map-get($options, font-size);
        height: map-get($options, height);
      }

      textarea.field {
        height: auto;
        min-height: map-get($options, height);
      }

      .placeholder {
        top: map-get($options, placeholder-offset);
        padding:
          nth(map-get($options, placeholder-padding), 1)
          nth(map-get($options, placeholder-padding), 2);
      }

      .note,
      .error {
        padding: 0 nth(map-get($options, padding), 2);
      }

      .field-icon {
        width: map-get($options, height);
      }

      &.-has-icon .field {
        padding-right: map-get($options, height);
      }

      .field:focus + .placeholder,
      &.-has-value .placeholder {
        transform: translateY(map-get($options, placeholder-offset-active));
      }
    }
  }

  &.-no-borders {
    .field {
      padding-left: 0;
      box-shadow: none;
    }

    .placeholder {
      &, &-text {
        padding-left: 0;
      }
    }

    .note,
    .error {
      padding: 0;

      &:first-of-type {
        margin-top: 0;
      }
    }
  }

  &.-has-error {
    .field {
      &, &:focus {
        box-shadow: inset 0 0 0 2px $red-50;
      }
    }

    .field:focus + .placeholder,
    &.-has-value .placeholder {
      color: $red-50;
    }
  }

  &.-disabled {
    &:not(.-no-borders) {
      .field {
        &, &:focus {
          cursor: default;
          background-color: $gray-10;
          box-shadow: inset 0 0 0 2px $gray-40;
        }
      }
      .placeholder-text {
        background-color: $gray-10;
      }
    }
  }
}
</style>
