<template>
  <div class="autocomplete" v-click-outside="() => this.isShowHints = false">
    <base-input
      v-model="inputValue"
      v-bind="$attrs"
      v-on="listeners"
      ref="inputComponent">
      <template #icon>
        <svg-icon icon="search" :size="18"/>
      </template>
    </base-input>

    <div
      v-if="hasHints && isShowHints"
      class="autocomplete-hints">
      <div
        v-for="(hint, index) in hintsFiltered"
        :key="index"
        @click.prevent="onSelect(hint)"
        class="hint-item">
        <slot name="hint" :hint="hint">
          <div class="hint-text" v-html="hint.valueHtml || hint[textFieldName]"></div>
        </slot>
      </div>
    </div>
  </div>
</template>

<script>
import BaseInput from '@/components/BaseInput';
import ClickOutside from '@/directives/ClickOutside';

export default {
  name: 'TextField',
  components: {
    BaseInput,
  },
  props: {
    value: {
      type: String,
      default: '',
    },
    searchHints: {
      type: Array,
      default: () => [],
    },
    minValueLength: {
      type: Number,
      default: 2,
    },
    delay: {
      type: Number,
      default: 300,
    },
    isAsync: {
      type: Boolean,
      default: true,
    },
    textFieldName: {
      type: String,
      default: 'text',
    },
  },
  data: () => ({
    inputValue: '',
    timeout: null,
    isShowHints: false,
    hasFocus: false,
  }),
  computed: {
    hasHints () {
      return this.value.length > 0
        && this.inputValue.length >= this.minValueLength
        && this.hintsFiltered.length > 0
        && this.isShowHints;
    },
    listeners () {
      return {
        ...this.$listeners,
        input: () => {
          this.updateAutocomplete();
        },
        focus: () => {
          this.isShowHints = true;
          this.hasFocus = true;
        },
        blur: () => {
          this.hasFocus = false;
        },
        'icon-click': () => {
          clearTimeout(this.timeout);
          this.isShowHints = false;
          this.$emit('on-search', this.inputValue);
        },
      };
    },
    hintsFiltered () {
      if (this.inputValue.length <= this.minValueLength) {
        return [];
      }
      if (this.isAsync) {
        return this.searchHints;
      }

      const currentValue = this.inputValue.toLowerCase();

      return this.searchHints
        .filter((hint) => hint[this.textFieldName].toLowerCase().indexOf(currentValue) > -1)
        .map((hint) => {
          const newHint = { ...hint };
          newHint.valueHtml = this.wrapFoundWord(hint[this.textFieldName], currentValue);
          return newHint;
        });
    },
  },
  watch: {
    value: {
      immediate: true,
      handler (val) {
        this.inputValue = val;
      },
    },
    searchHints: {
      immediate: true,
      handler () {
        if (this.hasFocus) {
          this.isShowHints = true;
        }
      },
    },
  },
  methods: {
    updateAutocomplete () {
      this.$emit('input', this.inputValue);

      clearTimeout(this.timeout);

      this.isShowHints = false;

      if (this.inputValue.length <= this.minValueLength) {
        return false;
      }

      this.timeout = setTimeout(() => {
        if (this.isAsync) {
          this.$emit('on-search', this.inputValue);
        } else {
          this.isShowHints = true;
        }
      }, this.delay);
    },
    onSelect (hint) {
      this.inputValue = hint[this.textFieldName];
      this.$refs.inputComponent.focus();

      this.$emit('input', this.inputValue);
      this.$emit('on-select', hint);

      this.updateAutocomplete();
    },
    wrapFoundWord (string, word) {
      return string.replace(new RegExp(word, 'gi'), (match) => `<strong>${match}</strong>`);
    },
  },
  directives: {
    'click-outside': ClickOutside,
  },
};
</script>

<style lang="scss" scoped>
  .autocomplete {
    position: relative;

    &-hints {
      position: absolute;
      top: 100%;
      left: 0;
      width: 100%;
      background-color: $white;
      box-shadow: 0 2px 8px rgba($black, .25);
      border-radius: 6px;
      z-index: 99;
      padding: 10px 0;
      font-size: 14px;
      line-height: 18px;
      overflow-x: hidden;
      overflow-y: auto;
      max-height: 270px;
      @include scrollbar(4px, 6px, $gray-30, transparent);
    }

    .hint {
      &-item {
        padding: 8px 12px;
        cursor: pointer;

        &:hover {
          background-color: $gray-20;
        }
      }

      &-text:not(:last-child) {
        margin-right: 16px;
      }

      &-text {
        min-width: 0;
      }
    }
  }
</style>
