<template>
  <div class="tab" v-if="isActive">
    <slot></slot>
  </div>
</template>

<script>
export default {
  name: 'Tab',
  props: {
    name: {
      type: [String, Number],
      required: true,
    },
  },
  data: () => ({
    isActive: false,
  }),
  watch: {
    '$parent.activeTabLocal': {
      immediate: true,
      handler(val) {
        this.isActive = `${val}` === `${this.name}`;
      },
    },
  },
};
</script>
