<template>
  <span :class="computedClasses">
    <svg
      xmlns="http://www.w3.org/2000/svg"
      viewBox="0 0 20 20">
      <path d="M10 0V2C14.41 2 18 5.59 18 10C18 14.41 14.41 18 10 18C5.59 18 2 14.41 2
      10H0C0 15.52 4.48 20 10 20C15.52 20 20 15.52 20 10C20 4.48 15.52 0 10 0Z">
        <animateTransform
          attributeName="transform"
          type="rotate"
          calcMode="linear"
          values="0 10 10;360 10 10"
          keyTimes="0;1"
          dur="1s"
          begin="0s"
          repeatCount="indefinite"/>
      </path>
    </svg>
  </span>
</template>

<script>
export default {
  name: 'Preloader',
  props: {
    baseClass: {
      type: String,
      default: 'preloader',
    },
    size: {
      type: String,
      default: 'small',
    },
  },
  computed: {
    computedClasses () {
      return [
        this.baseClass,
        this.sizeModifier,
      ];
    },
    sizeModifier () {
      return (this.size !== '') ? `-${this.size}` : '';
    },
  },
};
</script>

<style lang="scss" scoped>
$sizes: (
  large: (
    width: 56px,
    height: 56px,
    stroke-width: 2,
  ),
  medium: (
    width: 40px,
    height: 40px,
    stroke-width: 4,
  ),
  small: (
    width: 28px,
    height: 28px,
    stroke-width: 8,
  ),
);

.preloader {
  display: block;
  margin: auto;
  width: 56px;
  height: 56px;
  flex-shrink: 0;
  fill: $gray-60;

  @each $name, $options in $sizes {
    &.-#{$name} {
      width: map-get($options, width);
      height: map-get($options, height);
      stroke-width: map-get($options, stroke-width);
    }
  }
}
</style>
