<template>
  <div
    :class="computedClasses"
    v-click-outside="showOptions.bind(null, false)"
    @keydown.tab="showOptions(false)"
    @keydown.esc="showOptions(false)"
    @keydown.enter.prevent.stop="keydown('enter')"
    @keydown.up.prevent="keydown('up')"
    @keydown.down.prevent="keydown('down')"
    @blur="activeOptionIndex = null"
    :tabindex="disabled ? '-1' : tabindex">

    <div
      v-if="!hasTriggerSlot"
      class="select-field"
      @click.prevent="showOptions(!isOpen)">
      <span v-if="placeholder" class="placeholder">
        <span class="placeholder-text">
          <span>{{ placeholder }}</span>
          <span v-if="required" class="asterisk"> *</span>
        </span>
      </span>

      <span class="value">{{ textValue }}</span>

      <span class="icon-arrow">
        <svg-icon icon="drop-down"></svg-icon>
      </span>
    </div>

    <div
      v-else
      @click.prevent="showOptions(!isOpen)">
      <slot name="trigger"></slot>
    </div>

    <div v-if="hasNoteSlot" class="note">
      <slot name="note"></slot>
    </div>

    <div v-if="hasErrorSlot" class="error">
      <slot name="error"></slot>
    </div>

    <div
      v-if="!disabled"
      @click.prevent.stop
      ref="optionsContainer"
      class="select-options">
      <div
        v-if="searchOptions"
        class="search-item">
        <base-input
          v-model="searchValue"
          size="small"
          @input="$emit('on-filtered', preparedOptions)"
          placeholder="Найти"/>
      </div>

      <div
        v-if="multiple"
        @click.prevent.stop="selectAll"
        @keydown.enter="selectAll"
        class="item">
        <checkbox
          class="item-checkbox"
          v-model="checkedAll"/>
        <span>Выбрать все</span>
      </div>

      <template v-if="preparedOptions.length > 0">
        <div
          v-for="(option, index) in preparedOptions"
          :key="index"
          @click.prevent.stop="select(option)"
          :class="['item', {'-focus': activeOptionIndex !== null && activeOptionIndex === index}]"
          data-hint>
          <checkbox
            class="item-checkbox"
            v-if="multiple"
            v-model="checkedValue"
            :value="option[valueFieldName]"/>

          <slot name="option" :option="option">
            <span class="item-text">{{ option[textFieldName] }}</span>
          </slot>
        </div>
      </template>

      <div v-else class="empty-text">Ничего не найдено</div>
    </div>
  </div>
</template>

<script>
import Checkbox from '@/components/Checkbox';
import BaseInput from '@/components/BaseInput';
import ClickOutside from '@/directives/ClickOutside';

export default {
  name: 'BaseSelect',
  components: {
    Checkbox,
    BaseInput,
  },
  model: {
    prop: 'value',
    event: 'change',
  },
  props: {
    value: {
      type: null,
    },
    options: {
      type: Array,
      default: () => [],
    },
    placeholder: {
      type: String,
      default: '',
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    multiple: {
      type: Boolean,
      default: false,
    },
    required: {
      type: Boolean,
      default: false,
    },
    size: {
      type: String,
      default: '',
    },
    baseClassName: {
      type: String,
      default: 'select',
    },
    textFieldName: {
      type: String,
      default: 'text',
    },
    valueFieldName: {
      type: String,
      default: 'value',
    },
    searchOptions: {
      type: Boolean,
      default: false,
    },
    tabindex: {
      type: String,
      default: '1',
    },
  },
  data: () => ({
    isOpen: false,
    selectedValue: null,
    checkedValue: [],
    checkedAll: false,
    searchValue: '',
    activeOptionIndex: null,
  }),
  watch: {
    value: {
      immediate: true,
      handler (val) {
        if (!val) {
          this.selectedValue = (this.multiple) ? [] : '';
        } else {
          this.selectedValue = val;

          if (this.multiple) {
            this.prepareCheckedValues();
          }
        }
      },
    },
    isCheckedAll: {
      immediate: true,
      handler (val) {
        this.checkedAll = val;
      },
    },
  },
  computed: {
    computedClasses () {
      return [
        this.baseClassName,
        this.activeModifier,
        this.sizeModifier,
        this.hasValueModifier,
        this.errorModifier,
        this.disabledModifier,
      ];
    },
    sizeModifier () {
      return (this.size !== '') ? `-${this.size}` : '';
    },
    activeModifier () {
      return this.isOpen ? '-active' : '';
    },
    hasValueModifier () {
      const hasValue = Array.isArray(this.selectedValue)
        ? (this.selectedValue.length !== 0)
        : this.selectedValue;
      return hasValue ? '-has-value' : '';
    },
    errorModifier () {
      return this.hasErrorSlot ? '-has-error' : '';
    },
    disabledModifier () {
      return this.disabled ? '-disabled' : '';
    },
    textValue () {
      return (this.multiple) ? this.getMultipleText() : this.getText();
    },
    isCheckedAll () {
      return this.options.length === this.selectedValue.length;
    },
    hasNoteSlot () {
      return !!this.$slots.note;
    },
    hasErrorSlot () {
      return !!this.$slots.error;
    },
    hasTriggerSlot () {
      return !!this.$slots.trigger;
    },
    preparedOptions () {
      if (this.searchOptions && this.searchValue.length > 0) {
        return this.options
          .filter((option) => {
            const value = this.searchValue.toLowerCase();
            const optionText = option[this.textFieldName].toLowerCase();
            return optionText.indexOf(value) !== -1;
          });
      }

      return this.options;
    },
  },
  methods: {
    showOptions (isShow) {
      if (this.disabled) {
        return false;
      }

      this.isOpen = isShow;
    },
    select (item) {
      if (this.multiple) {
        const index = this.checkedValue.indexOf(item[this.valueFieldName]);
        if (index > -1) {
          this.checkedValue.splice(index, 1);
        } else {
          this.checkedValue.push(item[this.valueFieldName]);
        }

        this.selectedValue = this.options
          .filter((option) => this.checkedValue.includes(option[this.valueFieldName]));

        this.prepareCheckedValues();
      } else {
        this.selectedValue = item;
        this.showOptions(false);
      }

      this.$emit('change', this.selectedValue);
    },
    selectAll () {
      if (this.isCheckedAll) {
        this.selectedValue = [];
        this.checkedValue = [];
      } else {
        this.selectedValue = [...this.options];
        this.prepareCheckedValues();
      }

      this.$emit('change', this.selectedValue);
    },
    prepareCheckedValues () {
      this.checkedValue = this.selectedValue.map((option) => option[this.valueFieldName]);
    },
    getText () {
      if (!this.selectedValue || this.options.length === 0) {
        return '';
      }

      const data = this.options
        .find((option) => option[this.valueFieldName] === this.selectedValue[this.valueFieldName]);

      return data[this.textFieldName];
    },
    getMultipleText () {
      if (this.isCheckedAll) {
        return 'Выбрано все';
      }
      if (this.selectedValue.length > 2) {
        return `Выбрано ${this.selectedValue.length}`;
      }
      return this.selectedValue.map((item) => item[this.textFieldName]).join(', ');
    },
    keydown (key) {
      switch (key) {
        case 'enter':
          if (this.activeOptionIndex !== null && this.isOpen) {
            this.select(this.preparedOptions[this.activeOptionIndex]);
          } else {
            this.showOptions(!this.isOpen);
          }
          break;

        case 'up':
          if (this.isOpen) {
            if (
              this.activeOptionIndex === null
              || this.activeOptionIndex - 1 < 0
            ) {
              this.activeOptionIndex = this.preparedOptions.length - 1;
            } else {
              this.activeOptionIndex -= 1;
            }
            this.$nextTick(this.scrollOptions);
          }
          break;

        case 'down':
          if (this.isOpen) {
            if (
              this.activeOptionIndex === null
              || this.activeOptionIndex + 1 > this.preparedOptions.length - 1
            ) {
              this.activeOptionIndex = 0;
            } else {
              this.activeOptionIndex += 1;
            }
            this.$nextTick(this.scrollOptions);
          } else {
            this.showOptions(true);
          }
          break;
        default:
      }
    },
    scrollOptions () {
      const hintElement = document.querySelector('.-focus[data-hint]');
      if (hintElement) {
        this.$refs.optionsContainer.scrollTop = hintElement.offsetTop;
      }
    },
  },
  directives: {
    'click-outside': ClickOutside,
  },
};
</script>

<style lang="scss">
$border-radius: 6px;
$select: (
  font-family: $global-font-family,
  font-style: normal,
  font-weight: normal,
  font-size: 16px,

  placeholder-font-size-active: 12px,
  note-font-size: 12px,
  note-padding: 16px,

  padding: (11px, 30px, 11px, 16px),
  height: 40px,
  placeholder-padding: (0, 30px, 0, 12px),
);
$sizes: (
  small: (
    padding: (8px, 30px, 8px, 15px),
    height: 32px,
    font-size: 14px,
    placeholder-padding: (0, 30px, 0, 12px),
    placeholder-offset: 9px,
    placeholder-offset-active: -15px,
    list-item-pading: (8px, 15px),
  ),
);

.select {
  position: relative;
  font-family: map-get($select, font-family);
  font-style: map-get($select, font-style);
  font-weight: map-get($select, font-weight);
  font-size: map-get($select, font-size);
  line-height: 1;
  outline: none;

  &-field {
    display: block;
    position: relative;
    width: 100%;
    border-radius: $border-radius;
    box-shadow: inset 0 0 0 1px $gray-60;
    cursor: pointer;
    padding:
      nth(map-get($select, padding), 1)
      nth(map-get($select, padding), 2)
      nth(map-get($select, padding), 3)
      nth(map-get($select, padding), 4);
    height: map-get($select, height);
    user-select: none;

    .placeholder {
      display: block;
      position: absolute;
      top: 13px;
      left: 0;
      max-width: 100%;
      padding:
        nth(map-get($select, placeholder-padding), 1)
        nth(map-get($select, placeholder-padding), 2)
        nth(map-get($select, placeholder-padding), 3)
        nth(map-get($select, placeholder-padding), 4);
      color: $gray-60;
      transition: transform .2s, font-size .2s, color .2s;
      user-select: none;
      z-index: 2;

      &-text {
        position: relative;
        padding: 0 4px;
        background-color: $white;
        border-radius: 2px;
        vertical-align: top;
        @include truncate(100%);
      }

      .asterisk {
        color: $red-50;
      }
    }

    .value {
      white-space: nowrap;
      width: 100%;
      overflow: hidden;
      color: $gray-90;
      padding-right: 8px;
      @include truncate(100%);
    }

    .icon-arrow {
      display: block;
      position: absolute;
      right: 16px;
      top: 50%;
      transform: translateY(-50%);
      width: 10px;
      height: 10px;
      color: $gray-70;
      font-size: 10px;
    }
  }

  &-options {
    display: none;
    position: absolute;
    left: 0;
    top: map-get($select, height);
    box-shadow: 0 2px 8px rgba($black, 0.25);
    margin: 0;
    width: 100%;
    list-style-type: none;
    padding: 9px 0;
    border-radius: $border-radius;
    overflow-x: hidden;
    overflow-y: auto;
    max-height: 270px;
    z-index: 99;
    background-color: $white;
    @include scrollbar(4px, 6px, $gray-30, transparent);

    .item {
      display: flex;
      align-items: center;
      width: 100%;
      cursor: pointer;
      line-height: 16px;
      padding: 16px 16px;

      &:hover, &.-focus {
        background-color: $gray-10;
      }

      &-text {
        min-width: 0;
      }

      &-checkbox {
        flex-shrink: 0;
      }
    }

    .search-item {
      padding: 16px 16px 8px;
    }
    .empty-text {
      padding: 8px 16px 16px;
      color: $gray-80;
      font-size: 14px;
      line-height: 1.2;
    }
  }

  .note,
  .error {
    margin-top: 5px;
    font-size: map-get($select, note-font-size);
    padding: 0 nth(map-get($select, padding), 4);
  }

  .note {
    color: $gray-80;
  }

  .error {
    color: $red-50;
  }

  &.-active .select-field .placeholder,
  &.-has-value .select-field .placeholder {
    color: $gray-80;
    font-size: map-get($select, placeholder-font-size-active);
    transform: translateY(-19px);
  }

  &.-active, &:focus {
    .select-field {
      box-shadow: inset 0 0 0 2px $gray-70;
    }
  }

  &.-active {
    .icon-arrow {
      transform: scale(-1) translateY(50%);
    }
    .select-options {
      display: block;
    }
  }

  @each $name, $options in $sizes {
    &.-#{$name} {
      font-size: map-get($options, font-size);

      .select-field {
        height: map-get($options, height);
        padding:
          nth(map-get($options, padding), 1)
          nth(map-get($options, padding), 2)
          nth(map-get($options, padding), 3)
          nth(map-get($options, padding), 4);

        .placeholder {
          top: map-get($options, placeholder-offset);
          padding:
            nth(map-get($options, placeholder-padding), 1)
            nth(map-get($options, placeholder-padding), 2)
            nth(map-get($options, placeholder-padding), 3)
            nth(map-get($options, placeholder-padding), 4);
        }
      }

      .select-options {
        top: map-get($options, height);

        .item {
          padding:
            nth(map-get($options, list-item-pading), 1)
            nth(map-get($options, list-item-pading), 2);
        }
      }

      .note,
      .error {
        padding: 0 nth(map-get($options, padding), 4);
      }

      &.-active .select-field .placeholder,
      &.-has-value .select-field .placeholder {
        transform: translateY(map-get($options, placeholder-offset-active));
      }
    }
  }

  &.-has-error {
    .select-field {
      &, &:hover {
        box-shadow: inset 0 0 0 2px $red-50;
      }
    }

    &.-active .select-field .placeholder,
    &.-has-value .select-field .placeholder {
      color: $red-50;
    }
  }

  &.-disabled {
    .select-field {
      &, &:hover {
        cursor: default;
        background-color: $gray-10;
        box-shadow: inset 0 0 0 2px $gray-40;
      }

      .placeholder-text {
        background-color: $gray-10;
      }
    }
  }
}
</style>
