/**
 * Переносит элемент в body
 * @param element - элемент, который переносим
 * @param parent - родитель для нового элемента.
 */
export const insertBody = (element, parent) => {
  const parentElement = parent || document.body;
  parentElement.insertBefore(element, parentElement.lastChild);
};

/**
 * Удаляет элемент из body
 * @param element - элемент, который переносим
 * @param parent - родитель для нового элемента.
 */
export const removeBody = (element, parent) => {
  const parentElement = parent || document.body;
  parentElement.removeChild(element);
};
