export default {
  getFormattedDate(value, separator = '.', inverse = false) {
    if (!value) {
      return false;
    }

    let date = '';
    let day = '';
    let month = '';
    let year = '';

    if ((!(/chrome/i.test(navigator.userAgent))
      && /webkit|safari|khtml/i.test(navigator.userAgent))) {
      date = value.split('-');
      day = date[2].substring(0, 2);
      [year, month] = date;
    } else {
      date = new Date(value);
      day = date.getDate() <= 9 ? `0${date.getDate()}` : date.getDate();
      month = date.getMonth() < 9 ? `0${date.getMonth() + 1}` : date.getMonth() + 1;
      year = date.getFullYear();
    }

    return inverse ? `${year}${separator}${month}${separator}${day}` : `${day}${separator}${month}${separator}${year}`;
  },
  getFormattedTime(value, separator = ':') {
    if (!value) {
      return false;
    }

    const date = new Date(value);
    const hours = date.getHours() > 9 ? date.getHours() : `0${date.getHours()}`;
    const minutes = date.getMinutes() > 9 ? date.getMinutes() : `0${date.getMinutes()}`;

    return `${hours}${separator}${minutes}`;
  },
};
