import Animation from '@/utils/Animation';

class Ripple {
  constructor (el, options) {
    this.target = el;
    this.default = options || {};
  }

  init () {
    this._createListeners();
  }

  _getSize () {
    return this.options.size;
  }

  _setSize (options) {
    const hasWidthProperty = Object.prototype.hasOwnProperty.call(this.default, 'width');
    const hasHeightProperty = Object.prototype.hasOwnProperty.call(this.default, 'height');
    const defaultOffsetWidth = hasWidthProperty ? this.default.width : 0;
    const defaultOffsetHeight = hasHeightProperty ? this.default.height : 0;
    const offsetWidth = defaultOffsetWidth || options.offset_width;
    const offsetHeight = defaultOffsetHeight || options.offset_height;

    this.options.size = Math.max(offsetWidth, offsetHeight);
  }

  _setCoordinates (options) {
    const hasCoordinatesProperty = Object.prototype.hasOwnProperty.call(this.default, 'coordinates');
    const defaultCoordinates = hasCoordinatesProperty ? this.default.coordinates : {};
    const offsetX = defaultCoordinates.length !== 0 ? defaultCoordinates.x : options.offset_x;
    const offsetY = defaultCoordinates ? defaultCoordinates.y : options.offset_y;
    const { size } = options;

    this.options.coordinates = {
      x: offsetX - size / 2,
      y: offsetY - size / 2,
    };
  }

  _getOptions () {
    return this.options;
  }

  _setOptions (options) {
    this.options = {
      tag: 'span' || options.tag,
      class: 'ripple' || options.class,
      scale: 2.5 || options.scale,
      show_animation_timing: 'easeIn' || options.show_animation_timing,
      show_animation_duration: 300 || options.show_animation_duration,
      hide_animation_timing: 'easeIn' || options.hide_animation_timing,
      hide_animation_duration: 300 || options.hide_animation_duration,
      hide_animation_delay: 100 || options.hide_animation_delay,
    };
  }

  _newRipple () {
    const options = this._getOptions();

    this.ripple = document.createElement(options.tag);
    this.ripple.className = options.class;
    this.ripple.style.width = `${options.size}px`;
    this.ripple.style.height = `${options.size}px`;
    this.ripple.style.top = `${options.coordinates.y}px`;
    this.ripple.style.left = `${options.coordinates.x}px`;
    this.target.prepend(this.ripple);

    this._startRipple(this.ripple);
  }

  _startRipple (ripple) {
    this.growth = true;

    const options = this._getOptions();
    const animation = new Animation({
      draw: (progress) => {
        ripple.style.transform = `scale(${options.scale * progress})`;

        if (this.growth) {
          ripple.style.opacity = progress;
        }
      },
      timing: options.show_animation_timing,
      duration: options.show_animation_duration,
    });

    animation.play();
  }

  _endRipple (ripple) {
    const options = this._getOptions();

    setTimeout(() => {
      this.growth = false;

      const { opacity } = ripple.style;
      const animation = new Animation({
        draw: (progress) => {
          // eslint-disable-next-line no-param-reassign
          ripple.style.opacity = opacity * (1 - progress);
        },
        timing: options.hide_animation_timing,
        duration: options.hide_animation_duration,
      });

      animation.play();
    }, options.hide_animation_delay);
  }

  _removeRipple (ripple) {
    if (typeof ripple === 'undefined') {
      return false;
    }

    this._endRipple(ripple);

    const options = this._getOptions();

    setTimeout(() => {
      ripple.remove();
    }, options.hide_animation_duration + options.hide_animation_delay + 100);
  }

  _createListeners () {
    this.target.addEventListener('mousedown', (event) => {
      this._setOptions();
      this._setSize({
        offset_width: this.target.offsetWidth,
        offset_height: this.target.offsetHeight,
      });

      this._setCoordinates({
        offset_x: event.offsetX,
        offset_y: event.offsetY,
        size: this._getSize(),
      });

      this._newRipple();
    });

    this.target.addEventListener('mouseup', () => {
      this._removeRipple(this.ripple);
    });

    this.target.addEventListener('mouseout', () => {
      this._removeRipple(this.ripple);
    });
  }
}

export default {
  inserted(element) {
    const ripple = new Ripple(element);
    ripple.init();
  },
};
