<template>
  <div
    ref="container"
    :class="computedClasses">
    <template v-if="!defaultScrollbar">
      <div
        ref="content"
        :style="contentStyles"
        @scroll="() => { moveVerticalTrack(); moveHorizontalTrack(); }"
        @mousewheel="moveVerticalTrack"
        @DOMMouseScroll="moveVerticalTrack"
        @touchmove="touchScrollHandler"
        @touchstart="touchScrollHandler"
        @touchend="touchScrollHandler"
        :class="['scrollbar-content', {'-grabbed': isGrabbed}]">
        <slot name="default"/>
      </div>

      <div
        ref="verticalTrack"
        @mousedown="verticalTrackHandler"
        :style="verticalTrackStylesComputed"
        class="track-vertical"></div>

      <div
        v-show="scrollHorizontalRatio < 1"
        ref="horizontalTrack"
        @mousedown="horizontalTrackHandler"
        :style="horizontalTrackStylesComputed"
        class="track-horizontal"></div>
    </template>

    <div
      v-else
      ref="content"
      :style="contentStyles"
      class="scrollbar-content">
      <slot name="default"/>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Scrollbar',
  props: {
    coords: {
      type: Object,
      default: null,
      validator: (value) => value.hasOwnProperty('x') && value.hasOwnProperty('y'),
    },
    trackSize: {
      type: Number,
      default: 8,
    },
    maxHeight: {
      type: String,
      default: 'none',
    },
    maxWidth: {
      type: String,
      default: '100%',
    },
    defaultScrollbar: {
      type: Boolean,
      default: false,
    },
    alwaysShowTracks: {
      type: Boolean,
      default: false,
    },
    scrollbarOffset: {
      type: Number,
      default: 10,
    },
  },
  data: () => ({
    touch: {
      attachment: false,
      lastPosition: { x: 0, y: 0 },
      position: { x: 0, y: 0 },
      difference: { x: 0, y: 0 },
      scrollLength: { x: 0, y: 0 },
      touchStart: { x: 0, y: 0 },
      timeout: 0,
      autoScrollTimeout: 300,
      animationSpeed: 15,
    },
    dragScroll: {
      lastPageX: 0,
      lastPageY: 0,
    },
    wheelScroll: {
      isScrollUp: false,
      distance: null,
      interval: null,
      scrollSpeed: 5,
      wheelDeltaValue: 150,
      animationSpeed: 10,
    },
    verticalTrackStyles: {},
    horizontalTrackStyles: {},
    scrollVerticalRatio: 0,
    scrollHorizontalRatio: 0,
    isGrabbed: false,
    scrollbarMinSize: 40,
  }),
  computed: {
    computedClasses () {
      return [
        'scrollbar-container',
        this.defaultScrollModifier,
        this.alwaysShowTracksModifier,
      ];
    },
    contentStyles () {
      return {
        'max-height': this.maxHeight,
        'max-width': this.maxWidth,
      };
    },
    verticalTrackStylesComputed () {
      return {
        width: `${this.trackSize}px`,
        margin: `${this.scrollbarOffset}px`,
        ...this.verticalTrackStyles,
      };
    },
    horizontalTrackStylesComputed () {
      return {
        height: `${this.trackSize}px`,
        margin: `${this.scrollbarOffset}px`,
        ...this.horizontalTrackStyles,
      };
    },
    defaultScrollModifier () {
      return this.defaultScrollbar ? '-default-scrollbar' : '';
    },
    alwaysShowTracksModifier () {
      return this.alwaysShowTracks ? '-always-show-tracks' : '';
    },
  },
  watch: {
    isGrabbed (isGrabbed) {
      this.$nextTick(() => {
        document.body.classList.toggle('-grabbed', isGrabbed);
      });
    },
    coords: {
      immediate: true,
      handler (value) {
        this.$nextTick(() => {
          this.scrollTo(value);
        });
      },
    },
  },
  methods: {
    scrollTo (coords, behavior = 'instant') {
      const { content } = this.$refs;

      if (content && coords) {
        content.scrollTo({
          top: coords.y,
          left: coords.x,
          behavior,
        });

        this.moveVerticalTrack();
        this.moveHorizontalTrack();
      }
    },
    verticalTrackHandler (event) {
      this.dragScroll.lastPageY = event.pageY;
      this.isGrabbed = true;

      document.addEventListener('mousemove', this.dragVerticalTrack);
      document.addEventListener('mouseup', this.dragVerticalTrack);

      return false;
    },
    dragVerticalTrack (event) {
      if (event.type === 'mousemove') {
        const delta = event.pageY - this.dragScroll.lastPageY;

        this.dragScroll.lastPageY = event.pageY;
        this.$refs.content.scrollTop += delta / this.scrollVerticalRatio;
      }

      if (event.type === 'mouseup') {
        this.isGrabbed = false;

        document.removeEventListener('mousemove', this.dragVerticalTrack);
        document.removeEventListener('mouseup', this.dragVerticalTrack);
      }
    },
    moveVerticalTrack (event) {
      const { content } = this.$refs;

      if (event && event.type !== 'scroll') {
        event.preventDefault();
        event.stopPropagation();

        this.wheelScrollHandler(event);
      }

      const totalHeight = content.scrollHeight;
      const ownHeight = content.clientHeight;

      this.scrollVerticalRatio = ownHeight / totalHeight;

      if (this.scrollVerticalRatio < 1) {
        let scrollbarHeight = this.scrollVerticalRatio * ownHeight;

        if (scrollbarHeight < this.scrollbarMinSize) {
          scrollbarHeight = this.scrollbarMinSize;
        }
        const scrollbarFullHeight = scrollbarHeight + this.scrollbarOffset * 2;

        this.verticalTrackStyles = {
          height: `${scrollbarHeight}px`,
          top: `${content.scrollTop / (totalHeight - ownHeight) * (ownHeight - scrollbarFullHeight)}px`,
          // top: `${content.scrollTop / (totalHeight + ownHeight + scrollbarFullHeight) * (100)}%`,
        };
      }
    },
    horizontalTrackHandler (event) {
      this.dragScroll.lastPageX = event.pageX;
      this.isGrabbed = true;

      document.addEventListener('mousemove', this.dragHorizontalTrack);
      document.addEventListener('mouseup', this.dragHorizontalTrack);

      return false;
    },
    dragHorizontalTrack (event) {
      if (event.type === 'mousemove') {
        const delta = event.pageX - this.dragScroll.lastPageX;

        this.dragScroll.lastPageX = event.pageX;
        this.$refs.content.scrollLeft += delta / this.scrollHorizontalRatio;
      }

      if (event.type === 'mouseup') {
        this.isGrabbed = false;

        document.removeEventListener('mousemove', this.dragHorizontalTrack);
        document.removeEventListener('mouseup', this.dragHorizontalTrack);
      }
    },
    moveHorizontalTrack () {
      const { content } = this.$refs;
      const totalWidth = content.scrollWidth;
      const ownWidth = content.clientWidth;

      this.scrollHorizontalRatio = ownWidth / totalWidth;

      if (this.scrollHorizontalRatio < 1) {
        let scrollbarWidth = this.scrollVerticalRatio * ownWidth;

        if (scrollbarWidth < this.scrollbarMinSize) {
          scrollbarWidth = this.scrollbarMinSize;
        }
        const scrollbarFullWidth = scrollbarWidth + this.scrollbarOffset * 2;

        this.horizontalTrackStyles = {
          width: `${scrollbarWidth}px`,
          left: `${(content.scrollLeft / (totalWidth - ownWidth)) * (ownWidth - scrollbarFullWidth)}px`,
        };
      }
    },
    touchScrollHandler (event) {
      const { content } = this.$refs;

      if (event.type === 'touchstart') {
        this.touch.attachment = true;
        this.touch.lastPosition = {
          x: event.touches[0].clientX,
          y: event.touches[0].clientY,
        };
        this.touch.touchStart = {
          x: event.touches[0].clientX,
          y: event.touches[0].clientY,
        };
        this.touch.timeout = Date.now();
      }

      if (event.type === 'touchend') {
        this.touch.attachment = false;
        this.touch.timeout = Date.now() - this.touch.timeout;

        if (this.touch.timeout < this.touch.autoScrollTimeout) {
          const delta = this.touch.timeout / this.touch.animationSpeed;

          content.scrollTo({
            top: content.scrollTop - delta * this.touch.scrollLength.y,
            left: content.scrollLeft - delta * this.touch.scrollLength.x,
            behavior: 'smooth',
          });
        }
      }

      if (event.type === 'touchmove' && this.touch.attachment === true) {
        this.touch.position = {
          x: event.touches[0].clientX,
          y: event.touches[0].clientY,
        };
        this.touch.difference = {
          x: this.touch.position.x - this.touch.lastPosition.x,
          y: this.touch.position.y - this.touch.lastPosition.y,
        };
        this.touch.scrollLength = {
          x: event.touches[0].clientX - this.touch.touchStart.x,
          y: event.touches[0].clientY - this.touch.touchStart.y,
        };

        content.scrollLeft -= this.touch.difference.x;
        content.scrollTop -= this.touch.difference.y;

        this.touch.lastPosition = {
          x: event.touches[0].clientX,
          y: event.touches[0].clientY,
        };
      }
    },
    wheelScrollHandler (event) {
      const { content } = this.$refs;
      let delta = 0;

      if (event.wheelDelta) {
        delta = event.wheelDelta / 2;
      } else if (event.detail) {
        const detail = event.detail > 1 ? -1 : 1;
        delta = detail * this.wheelScroll.wheelDeltaValue / 2;
      }

      this.wheelScroll.distance = content.scrollTop;
      this.wheelScroll.distance -= delta;
      this.wheelScroll.isScrollUp = delta > 0;

      if (this.wheelScroll.interval == null) {
        this.wheelScroll.interval = setInterval(() => {
          const step = Math.round(
            (this.wheelScroll.distance - content.scrollTop) / this.wheelScroll.scrollSpeed,
          );

          if (
            content.scrollTop <= 0
            || (content.scrollTop >= content.scrollHeight - content.clientHeight)
            || (this.wheelScroll.isScrollUp && step > -1)
            || (!this.wheelScroll.isScrollUp && step < 1)
          ) {
            clearInterval(this.wheelScroll.interval);
            this.wheelScroll.interval = null;
            this.wheelScroll.distance = null;
          }

          content.scrollTop += step;
        }, this.wheelScroll.animationSpeed);
      }
    },
  },
  mounted () {
    if (!this.defaultScrollbar) {
      window.addEventListener('resize', this.moveVerticalTrack);
      window.addEventListener('resize', this.moveHorizontalTrack);
      this.moveVerticalTrack();
      this.moveHorizontalTrack();
    }
  },
  beforeDestroy() {
    if (!this.defaultScrollbar) {
      window.removeEventListener('resize', this.moveVerticalTrack);
      window.removeEventListener('resize', this.moveHorizontalTrack);
    }
  },
};
</script>

<style lang="scss">
$scrollbar-color: $gray-30;

.scrollbar {
  &-container {
    position: relative;
    height: inherit;
    width: inherit;
    overflow: hidden;
  }

  &-content {
    position: relative;
    overflow: auto;
    width: 100%;
    height: 100%;
    -webkit-overflow-scrolling: touch;

  }

  &-container:not(.-default-scrollbar) {
    .scrollbar-content {
      scroll-behavior: unset;
      overflow: hidden;
      height: calc(100% + 20px);
      width: calc(100% + 20px);
    }

    &.-always-show-tracks,
    &:hover,
    &:active {
      .track-vertical,
      .track-horizontal {
        display: block;
      }
    }
  }
}

.track-vertical,
.track-horizontal {
  display: none;
  position: absolute;
  border-radius: 4px;
  cursor: default;
  background-color: $scrollbar-color;
  z-index: 2;
  //transition: all .03s linear;

  &.-hidden {
    display: none;
  }
}

.track-vertical {
  top: 0;
  right: 0;
}

.track-horizontal {
  bottom: 0;
  left: 0;
}
</style>

<style>
.-grabbed {
  user-select: none;
}
</style>
