<template>
  <div
    :class="['modal', computedClasses]"
    ref="modal"
    data-modal
    @click.prevent.stop="overlayClick">
    <div
      class="modal-content"
     :style="modalStyles">
      <div v-if="title" class="modal-title">{{ title }}</div>

      <a
        v-if="!disableClose"
        href="#"
        class="modal-close"
        @click.prevent.stop="$emit('on-close')">
        <svg-icon icon="cross" :size="12"/>
      </a>

      <div v-if="hasBodySlot" class="modal-body">
        <slot></slot>
      </div>
    </div>
  </div>
</template>

<script>
import { insertBody } from '@/utils/helpers';

export default {
  name: 'Modal',
  props: {
    title: {
      type: String,
      default: null,
    },
    width: {
      type: String,
      default: null,
    },
    hasCloseButton: {
      type: Boolean,
      default: true,
    },
    disableClose: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    computedClasses() {
      return [
        this.noTitleModifier,
      ];
    },
    noTitleModifier () {
      return (this.hasCloseButton && !this.title) ? '-no-title' : '';
    },
    modalStyles () {
      if (this.width) {
        return {
          width: this.width,
        };
      }
      return null;
    },
    hasBodySlot () {
      return !!this.$slots.default;
    },
  },
  methods: {
    overlayClick (event) {
      if (event.target.hasAttribute('data-modal') && !this.disableClose) {
        this.$emit('on-close');
      }
    },
  },
  mounted () {
    this.$nextTick(() => {
      document.body.classList.add('-no-scroll');
      insertBody(this.$refs.modal);
    });
  },
  beforeDestroy () {
    this.$nextTick(() => {
      document.body.classList.remove('-no-scroll');
    });
  },
};
</script>

<style lang="scss" scoped>
.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  z-index: 100;
  max-width: 100%;
  background: rgba($black, .5);
  overflow-x: hidden;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  text-align: center;
  padding: 26px;

  &:before {
    content: '';
    display: inline-block;
    height: 100%;
    vertical-align: middle;
    margin-right: -.36em;
  }

  &-content {
    display: inline-block;
    text-align: left;
    vertical-align: middle;
    position: relative;
    margin: 0 auto;
    background: $white;
    box-shadow: 0 2px 8px rgba($black, .25);
    border-radius: 6px;
    max-width: 100%;
    padding: 20px;
    width: 320px;
  }

  &-close {
    display: block;
    position: absolute;
    right: 0;
    top: 0;
    padding: 12px;
    line-height: 1;
    color: $gray-70;

    &:hover {
      color: $red-50;
    }
  }

  &-title {
    position: relative;
    font-family: $global-font-family;
    font-style: normal;
    font-weight: bold;
    font-size: 20px;
    line-height: 24px;
    padding: 0 40px 16px 0;
    margin-bottom: 24px;

    &:after {
      content: '';
      position: absolute;
      bottom: 0;
      right: -10px;
      left: -10px;
      height: 1px;
      background-color: $gray-50;
    }
  }

  &.-no-title {
    .modal-content {
      padding-right: 40px;
    }
  }
}
</style>

<style>
body.-no-scroll {
  overflow: hidden;
}
</style>
