<template>
  <div
    v-click-outside="closeCalendar"
    :class="[
      'calendar-wrapper',
      {'-active' : isCalendarVisible}
    ]">
    <base-input
      ref="inputComponent"
      v-if="!hideInput"
      v-model="date"
      v-bind="$attrs"
      :readonly="readonly || range"
      :mask="mask"
      masked
      v-on="listeners"
      @icon-click="iconClick">
      <template #icon>
        <svg-icon
          :icon="(date.length !== 0) ? 'cross' : 'calendar'"
          :size="(date.length !== 0) ? 12 : 20"/>
      </template>
    </base-input>

    <slot name="trigger"/>

    <div v-if="isCalendarVisible" class="calendar">
      <div class="header">
        <div
          v-show="isPrevArrowVisible"
          @click="showPrevMonth(calendar[0].date)"
          class="arrow -prev">
          <svg-icon icon="arrow-up"/>
        </div>

        <div
          v-for="(month, index) in calendar"
          :key="`header_${index}`"
          class="caption">{{ month.name }} {{ month.year }}</div>

        <div
          v-show="isNextArrowVisible"
          @click="showNextMonth(calendar[0].date)"
          class="arrow -next">
          <svg-icon icon="arrow-up"/>
        </div>
      </div>

      <div class="content">
        <div
          v-for="(month, index) in calendar"
          :key="`content_${index}`"
          :class="[
            'month',
            {'-last' : index === calendar.length -1}
          ]">
          <div class="days-of-week">
            <div
              v-for="(day, index) in daysOfTheWeek"
              :key="index"
              class="day-of-week">
              <span class="day-name">{{ day }}</span>
            </div>
          </div>

          <div
            v-for="(week, index) in month.weeks"
            :key="index"
            class="weeks">
            <div
              v-for="(day, index) in week"
              :key="index"
              @click="chooseDate(day)"
              :class="[
                'week',
                day.class,
                {'-last' : index === 6}
              ]">
              <span class="day-number">{{ day.number }}</span>
            </div>
          </div>

          <div v-if="buttons" class="save-buttons">
            <base-button
              variant="link"
              size="small"
              class="link"
              @click.prevent="undo">
              Отменить
            </base-button>

            <base-button
              variant="link"
              size="small"
              class="link"
              @click.prevent="toggleCalendar(true)">
              Сохранить
            </base-button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ClickOutside from '@/directives/ClickOutside';
import DateTimes from '@/utils/DateTimes';
import BaseInput from '@/components/BaseInput';
import BaseButton from '@/components/BaseButton';

const months = [
  'Январь', 'Февраль', 'Март', 'Апрель',
  'Май', 'Июнь', 'Июль', 'Август',
  'Сентябрь', 'Октябрь', 'Ноябрь', 'Декабрь',
];
const daysOfTheWeek = ['Пн', 'Вт', 'Ср', 'Чт', 'Пт', 'Сб', 'Вс'];
const cellModifiers = ['-active', '-selected', '-start', '-end'];
const currentDate = new Date();
const currentDay = currentDate.getDate();
const currentMonth = currentDate.getMonth();
const currentYear = currentDate.getFullYear();

export default {
  name: 'Calendar',
  components: {
    BaseInput,
    BaseButton,
  },
  directives: {
    'click-outside': ClickOutside,
  },
  props: {
    value: {
      type: Array,
      default: () => [],
    },
    dates: {
      type: Array,
      default: () => [],
    },
    min: {
      type: String,
      default: `${currentYear}-${currentMonth}-${currentDay}`,
    },
    max: {
      type: String,
      default: '',
    },
    multipleCalendar: {
      type: Boolean,
      default: false,
    },
    range: {
      type: Boolean,
      default: false,
    },
    buttons: {
      type: Boolean,
      default: false,
    },
    hideInput: {
      type: Boolean,
      default: false,
    },
    isMobile: {
      type: Boolean,
      default: false,
    },
    readonly: {
      type: Boolean,
      default: false,
    },
  },
  data: () => ({
    date: '',
    userSelect: [],
    isCalendarVisible: false,
    daysOfTheWeek,
    period: {
      start: {
        year: null,
        month: null,
        date: '',
      },
      end: {
        year: null,
        month: null,
        date: '',
      },
    },
    calendar: [],
    isLastDayChosen: true,
    firstRangeDate: '',
    lastRangeDate: '',
    mask: '',
    oldSelect: [],
  }),
  computed: {
    isPrevArrowVisible () {
      if (this.calendar.length === 0
        || !Object.prototype.hasOwnProperty.call(this.calendar[0], 'year')) {
        return false;
      }

      return this.period.start.year === this.calendar[0].year
        ? this.period.start.month !== this.calendar[0].index : true;
    },
    isNextArrowVisible () {
      const lastMonthObj = !this.isMobile && this.multipleCalendar
        ? this.calendar[1]
        : this.calendar[0];

      if (this.calendar.length === 0
        || (typeof lastMonthObj === 'undefined'
          && !Object.prototype.hasOwnProperty.call(lastMonthObj, 'year'))) {
        return false;
      }

      return (this.max.length !== 0 || this.dates.length !== 0)
        && this.period.end.year === lastMonthObj.year
        ? this.period.end.month !== lastMonthObj.index : true;
    },
    generateCalendarPeriod () {
      if (this.dates.length === 0) {
        return false;
      }

      const customMonths = {};

      this.dates.forEach((item) => {
        let date = item;
        if (typeof date === 'object') {
          date = DateTimes.getFormattedDate(date, '-', true);
        }
        const dayNumber = this.getDateFromString(date, 2);
        const month = this.getDateFromString(date, 1);
        const year = this.getDateFromString(date, 0);
        const iterator = `${year}_${month}`;

        customMonths[iterator] = typeof customMonths[iterator] === 'undefined'
          ? [dayNumber] : [...customMonths[iterator], dayNumber];
      });

      return customMonths;
    },
    listeners () {
      return {
        ...this.$listeners,
        input: (val) => {
          if (val.length > 0 && val.length !== 10) {
            return false;
          }
          if (val.length === 0) {
            return this.passDate([]);
          }
          const [day, month, year] = val.split('.');
          const date = new Date(`${year}-${month}-${day}T00:00:00Z`);
          if (!Number.isNaN(date.valueOf())) {
            this.chooseDate({ full_date: date });
            this.setCalendar(date);
            this.isCalendarVisible = true;
            this.passDate([date]);
          }
        },
        focus: (event) => {
          this.isCalendarVisible = true;
          this.$emit('focus', event);
        },
      };
    },
  },
  methods: {
    setInputMask () {
      this.mask = this.range ? 'С ##.##.#### по ##.##.####' : '##.##.####';
    },
    setMinMaxDates () {
      if (this.dates.length !== 0) {
        const years = this.dates.map((item) => this.getDateFromString(item, 0));

        const minYear = Math.min(...years);
        const maxYear = Math.max(...years);

        const minMonths = this.dates.map((item) => (minYear === this.getDateFromString(item, 0)
          ? this.getDateFromString(item, 1) : 12));
        const maxMonths = this.dates.map((item) => (maxYear === this.getDateFromString(item, 0)
          ? this.getDateFromString(item, 1) : 0));

        this.period.start.year = minYear;
        this.period.start.month = Math.min(...minMonths) - 1;
        this.period.end.year = maxYear;
        this.period.end.month = Math.max(...maxMonths) - 1;

        return false;
      }

      if (this.max !== '') {
        this.period.end.year = this.getDateFromString(this.max, 0);
        this.period.end.month = this.getDateFromString(this.max, 1);
      }

      this.period.start.year = this.getDateFromString(this.min, 0);
      this.period.start.month = this.getDateFromString(this.min, 1);
    },
    getDateFromString (dateString, index) {
      return Number(dateString.split(/-/)[index]);
    },
    getNextMonthDate (date) {
      const monthIndex = date.getMonth();
      const nextMonth = monthIndex === 11 ? 0 : monthIndex + 1;
      const nextYear = monthIndex === 11 ? date.getFullYear() + 1 : date.getFullYear();

      return new Date(nextYear, nextMonth, 1);
    },
    showPrevMonth (date) {
      if (!this.isPrevArrowVisible) {
        return false;
      }

      const monthIndex = date.getMonth();
      const prevMonth = monthIndex === 0 ? 11 : monthIndex - 1;
      const prevYear = monthIndex === 0 ? date.getFullYear() - 1 : date.getFullYear();
      const prevDate = new Date(prevYear, prevMonth, 1);

      this.setCalendar(prevDate);
    },
    showNextMonth (date) {
      if (!this.isNextArrowVisible) {
        return false;
      }

      this.setCalendar(this.getNextMonthDate(date));
    },
    setCalendar (date) {
      this.calendar = [];

      const selectedDate = typeof date !== 'undefined' ? new Date(date) : new Date();

      this.calendar.push(this.generateMonth(selectedDate));

      if (!this.isMobile && this.multipleCalendar) {
        this.calendar.push(this.generateMonth(this.getNextMonthDate(selectedDate)));
      }
    },
    generateMonth (date) {
      const weeks = {};
      let elements = [];
      const monthName = this.getMonthNameByIndex(date.getMonth());
      const firstDay = new Date(date.getFullYear(), date.getMonth(), 1);
      const lastDay = new Date(date.getFullYear(), date.getMonth() + 1, 0);
      const days = lastDay.getDate() - firstDay.getDate();

      const getCustomPeriod = `${date.getFullYear()}_${date.getMonth() + 1}`;
      const activeDays = this.generateCalendarPeriod[getCustomPeriod] || [];

      for (let i = 0, j = 0; i <= days; i += 1) {
        if (j === 0 && i === 0) {
          const emptyCounter = firstDay.getDay() > 0 ? firstDay.getDay() : 7;

          for (let a = 0; a < emptyCounter - 1; a += 1) {
            elements.push({
              number: '',
              class: '-prev',
              is_disable: true,
            });
          }
        }

        const iterationDate = new Date(date);

        iterationDate.setDate(firstDay.getDate() + i);
        elements.push({
          full_date: iterationDate,
          number: this.addZeroToDay(iterationDate.getDate()),
          class: this.getModifierClass(iterationDate, activeDays),
          is_disable: !this.disableCustomDate(iterationDate, activeDays),
        });

        if (iterationDate.getDay() === 0 || i === days) {
          weeks[j += 1] = elements;
          elements = [];
        }
      }

      return {
        date,
        year: date.getFullYear(),
        index: date.getMonth(),
        name: monthName,
        weeks,
      };
    },
    getMonthNameByIndex (index) {
      return months[index];
    },
    addZeroToDay (day) {
      return day < 10 ? `0${day}` : day;
    },
    getModifierClass (iterationDate, activeDays) {
      let modifierClass = '';

      if (this.dates.length !== 0
          && this.disableCustomDate(iterationDate.getDate(), activeDays)) {
        modifierClass = '-prev';
      }

      if (iterationDate.getFullYear() === this.getDateFromString(this.min, 0)
          && iterationDate.getMonth() === this.getDateFromString(this.min, 1)
          && iterationDate.getDate() < this.getDateFromString(this.min, 2)) {
        modifierClass = '-prev';
      }

      if (iterationDate.getFullYear() === currentYear
          && iterationDate.getMonth() === currentMonth
          && iterationDate.getDate() === currentDay) {
        modifierClass = '-current';
      }

      const formattedIterationDate = new Date(iterationDate).setHours(0, 0, 0, 0);
      const startDate = new Date(this.userSelect[0]).setHours(0, 0, 0, 0);
      const endDate = new Date(this.userSelect[1]).setHours(0, 0, 0, 0);

      if (formattedIterationDate === startDate || formattedIterationDate === endDate) {
        modifierClass += ' -active';
      }

      if (this.period && this.isLastDayChosen) {
        modifierClass += this.getRangeClasses(formattedIterationDate, startDate, endDate);
      }

      return modifierClass;
    },
    disableCustomDate (verifiableDay, activeDays) {
      return !activeDays.includes(verifiableDay);
    },
    chooseDate (date) {
      if (Object.prototype.hasOwnProperty.call(date, 'is_disable') && date.is_disable) {
        return false;
      }

      if (Object.prototype.hasOwnProperty.call(date, 'class')
        && date.class.includes('-active')
        && !this.range) {
        return false;
      }

      if (this.isLastDayChosen) {
        this.removeActiveDays();
      }

      if (this.range) {
        this.isLastDayChosen = !this.isLastDayChosen;

        if (this.userSelect.length > 1) {
          this.oldSelect = [...this.userSelect];
        }

        if (this.isLastDayChosen) {
          this.removeActiveDays();
          this.lastRangeDate = date.full_date;

          const period = this.generateRangeOfDate(this.firstRangeDate, this.lastRangeDate);

          this.setSelectedClass(period);
          this.userSelect = period;

          if (!this.buttons) {
            this.date = `С ${DateTimes.getFormattedDate(period[0], '.')} по ${DateTimes.getFormattedDate(period[1], '.')}`;
            this.toggleCalendar(true);
          }

          this.firstRangeDate = '';
          this.lastRangeDate = '';
        } else {
          this.firstRangeDate = date.full_date;
          this.userSelect = [date.full_date];
        }
      } else {
        this.oldSelect = [...this.userSelect];
        this.userSelect = [date.full_date];

        if (!this.buttons) {
          this.date = DateTimes.getFormattedDate(date.full_date, '.');
          this.toggleCalendar(true);
        }
      }

      date.class += ' -active';
    },
    removeActiveDays () {
      Object.keys(this.calendar).forEach((month) => {
        Object.keys(this.calendar[month].weeks).forEach((week) => {
          Object.keys(this.calendar[month].weeks[week]).forEach((day) => {
            const classes = this.calendar[month].weeks[week][day].class.split(' ');
            const sortClasses = classes.map((className) => (cellModifiers.includes(className) ? '' : className));

            this.calendar[month].weeks[week][day].class = sortClasses.join(' ');
          });
        });
      });
    },
    generateRangeOfDate (firstDate, lastDate) {
      return new Date(firstDate) > new Date(lastDate)
        ? [lastDate, firstDate] : [firstDate, lastDate];
    },
    setSelectedClass (period) {
      const startDate = period[0];
      const endDate = period[1];

      if (startDate !== endDate) {
        Object.keys(this.calendar).forEach((month) => {
          Object.keys(this.calendar[month].weeks).forEach((week) => {
            Object.keys(this.calendar[month].weeks[week]).forEach((day) => {
              const date = this.calendar[month].weeks[week][day];
              date.class += this.getRangeClasses(date.full_date, startDate, endDate);
            });
          });
        });
      }
    },
    getRangeClasses (iterationDate, startDate, endDate) {
      const formattedIterationDate = new Date(iterationDate).setHours(0, 0, 0, 0);
      const formattedStartDate = new Date(startDate).setHours(0, 0, 0, 0);
      const formattedEndDate = new Date(endDate).setHours(0, 0, 0, 0);

      if (formattedStartDate !== formattedEndDate
          && (formattedIterationDate >= formattedStartDate
          && formattedIterationDate <= formattedEndDate)) {
        if (formattedIterationDate === formattedStartDate) {
          return ' -start';
        } if (formattedIterationDate === formattedEndDate) {
          return ' -end';
        }
        return ' -selected';
      }
      return '';
    },
    closeCalendar () {
      this.isCalendarVisible = false;
    },
    toggleCalendar (isPassDate = false) {
      this.isCalendarVisible = !this.isCalendarVisible;
      if (this.isCalendarVisible) {
        this.$refs.inputComponent.focus();
      }

      if (isPassDate) {
        this.passDate(this.userSelect);
      }
    },
    undo() {
      this.userSelect = [...this.oldSelect];
      this.setCalendar();
      this.toggleCalendar();
    },
    passDate (date) {
      this.$emit('input', date);
    },
    iconClick () {
      if (this.date.length !== 0) {
        this.date = '';
        this.userSelect = [];
        this.setCalendar();
        this.$refs.inputComponent.focus();
        if (!this.buttons) this.passDate([]);
      } else {
        this.toggleCalendar();
      }
    },
  },
  mounted () {
    this.setInputMask();
    this.setMinMaxDates();
    this.setCalendar();
  },
  watch: {
    min: {
      immediate: true,
      handler (min) {
        this.period.start.year = this.getDateFromString(min, 0);
        this.period.start.month = this.getDateFromString(min, 1);
      },
    },
    value: {
      immediate: true,
      handler (value) {
        if (typeof value === 'undefined' || value.length === 0) {
          return false;
        }

        this.date = this.range
          ? `С ${DateTimes.getFormattedDate(value[0], '.')} по ${DateTimes.getFormattedDate(value[1], '.')}`
          : DateTimes.getFormattedDate(value[0], '.');
        this.userSelect = value;
        this.setCalendar();
      },
    },
  },
};
</script>

<style lang="scss" scoped>
.calendar-wrapper {
  position: relative;
}

.calendar {
  user-select: none;
  position: absolute;
  z-index: 10;
  left: 0;
  top: 100%;
  background: $white;
  border-radius: 6px;
  box-shadow: 0 2px 8px 0 rgba($black, .25);
  font-size: 14px;
  line-height: 1;
}

.header {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 24px 0 20px;
  box-sizing: border-box;
}

.arrow {
  display: block;
  position: absolute;
  top: 50%;
  color: $gray-70;
  cursor: pointer;
  z-index: 2;
  padding: 12px;
  font-size: 20px;

  &:hover {
    color: $red-50;
  }

  &.-prev {
    left: 0;
    transform: translateY(-50%) rotate(-90deg);
  }

  &.-next {
    right: 0;
    transform: translateY(-50%) rotate(90deg);
  }
}

.caption {
  text-align: center;
  font-weight: bold;
  font-size: 18px;
  line-height: 1;
  padding: 0 48px;
  width: 100%;
}

.content {
  display: flex;
}

.month {
  width: 320px;
  padding: 0 12px 4px;
  margin: 0;

  &:nth-child(2) {
    margin-left: 16px;
  }
}

.days-of-week,
.weeks {
  padding: 0 0 8px;
  border: 0;
  display: flex;
}

.day-of-week,
.week {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  margin-right: 12px;
}

.day-of-week {
  font-weight: bold;
  &:last-child {
    margin-right: 0;
  }
}

.day-name {
  color: $gray-50;
  pointer-events: none;
  cursor: default;
}

.week {
  cursor: pointer;
  position: relative;
  display: flex;
  align-items: center;

  &:before,
  &:after {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 1;
  }

  .day-number {
    position: relative;
    z-index: 2;
  }

  &.-prev {
    pointer-events: none;
    cursor: default;

    .day-number {
      opacity: 0.5;
    }
  }

  &.-current {
    &:before {
      content: '';
      background: $gray-20;
      border-radius: 16px;
    }
  }

  &.-last {
    margin-right: 0;

    .day-number {
      color: $red-50;
    }
  }

  &.-selected,
  &.-active,
  &.-start,
  &.-end {
    &:before {
      background: $red-50;
      content: '';
    }

    .day-number {
      color: $white;
    }
  }

  &.-active {
    &:before {
      border-radius: 16px;
    }
  }

  &.-start {
    &:before {
      border-radius: 16px 0 0 16px;
    }

    &:not(.-last) {
      &:after {
        background: $red-50;
        content: '';
        left: 12px;
      }
    }
  }

  &.-end {
    &:before {
      border-radius: 0 16px 16px 0;
    }
  }

  &.-selected {
    &:before {
      min-width: 44px;
      border-radius: 0;
    }
  }

  &:last-child:before {
    min-width: 32px;
  }

  &:hover:not(.-start):not(.-end):not(.-selected) {
    &:before {
      background: $red-50;
      content: '';
      border-radius: 16px;
    }

    .day-number {
      color: $white;
    }
  }
}

.save-buttons {
  display: flex;
  justify-content: flex-end;
  padding-bottom: 12px;
  margin-top: 4px;

  .link:first-child {
    margin-right: 16px;
  }
}
</style>
