<template>
  <div class="tabs-wrapper">
    <scrollbar :track-size="6">
      <ul :class="computedClasses" ref="list">
        <li
          v-for="(tab, index) in tabs"
          :key="index"
          class="tabs-item">
          <a
            href="#"
            :class="['tabs-link', {
              '-active': tab[valueFieldName] === activeTabLocal && !tab.disabled,
              '-disabled': tab.disabled,
            }]"
            @click.prevent="toggleTab($event, tab)">
            <slot name="option" :option="tab">
              <span class="tabs-text">{{ tab[titleFieldName] }}</span>
            </slot>
          </a>
        </li>
      </ul>
    </scrollbar>
    <slot></slot>
  </div>
</template>

<script>
import { Scrollbar } from '@/components/Scrollbar';

export default {
  name: 'Tabs',
  components: { Scrollbar },
  props: {
    color: {
      type: String,
      default: '',
    },
    baseClassName: {
      type: String,
      default: 'tabs',
    },
    tabs: {
      type: Array,
      default: () => [],
      required: true,
    },
    titleFieldName: {
      type: String,
      default: 'title',
    },
    valueFieldName: {
      type: String,
      default: 'value',
    },
    activeTab: {
      type: [Number, String],
      default: null,
    },
  },
  data: () => ({
    activeTabLocal: 0,
  }),
  computed: {
    computedClasses() {
      return [
        this.baseClassName,
        this.colorModifier,
      ];
    },
    colorModifier () {
      return (this.color !== '') ? `-${this.color}` : '';
    },
  },
  watch: {
    activeTab: {
      immediate: true,
      handler (value) {
        if (value) {
          this.activeTabLocal = value;
        }
      },
    },
    tabs: {
      immediate: true,
      handler (arr) {
        if (!this.activeTab) {
          this.activeTabLocal = arr[0][this.valueFieldName];
        }
      },
    },
    activeTabLocal: {
      immediate: true,
      handler: 'updateScroll',
    },
  },
  methods: {
    toggleTab (event, tab) {
      if (this.activeTabLocal !== tab[this.valueFieldName] && !tab.disabled) {
        this.activeTabLocal = tab[this.valueFieldName];
        this.$emit('click', tab, event);
      }

      if (tab.disabled) {
        this.$emit('click-disabled', tab, event);
      }
    },
    updateScroll() {
      this.$nextTick(() => {
        const el = this.$refs.list.querySelector('a.-active');
        if (!el) return;
        el.scrollIntoView({ inline: 'center', block: 'end' });
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.scrollbar-container {
  padding-bottom: 6px;

  &:after {
    content: '';
    display: block;
    position: absolute;
    bottom: 6px;
    height: 1px;
    width: 100%;
    background-color: $gray-30;
    z-index: -1;
  }
}

.tabs {
  position: relative;
  display: flex;
  flex-wrap: nowrap;
  width: 100%;
  list-style-type: none;
  padding: 0;
  margin: 0;
  font-family: $global-font-family;
  font-weight: 500;
  font-size: 14px;
  line-height: 1;
  text-transform: uppercase;

  &-link {
    overflow: hidden;
    position: relative;
    display: block;
    text-decoration: none;
    padding: 16px;
    color: $gray-80;
    border-bottom: 2px solid transparent;
    flex-shrink: 0;

    &.-active {
      color: $gray-90;
      border-bottom-color: $gray-90;
      cursor: default;
    }

    &.-disabled {
      cursor: default;
      color: $gray-50;
      border-bottom-color: transparent;
    }
  }

  ::v-deep .ripple {
    position: absolute;
    border-radius: 50%;
    width: 0;
    height: 0;
    pointer-events: none;
    transform: scale(0);
    z-index: -1;
    background-color: $gray-10;
  }

  &.-secondary .tabs-link.-active:not(.-disabled) {
    color: $red-50;
    border-bottom-color: $red-50;
  }
}
</style>
