export default class Animation {
  constructor (options) {
    this._setOptions(options);
    this._setTimingFunction();

    if (this.options.draw === 'undefined') {
      return false;
    }
  }

  _setOptions (options) {
    this.options = {
      draw: options.draw,
      duration: options.duration || 300,
      timing: options.timing || 'linear',
    };
  }

  play () {
    const start = performance.now();
    const animate = (time) => {
      let timeFraction = (time - start) / this.options.duration;

      if (timeFraction > 1) {
        timeFraction = 1;
      }

      const progress = this.timingFunctions[this.options.timing](timeFraction);

      this.options.draw(progress);

      if (timeFraction < 1) {
        requestAnimationFrame(animate);
      }
    };

    requestAnimationFrame(animate);
  }

  _setTimingFunction () {
    this.timingFunctions = {
      linear: (t) => t,
      easeIn: (t) => t * t,
      easeOut: (t) => t * (2 - t),
      easeInOut: (t) => (t < 0.5 ? 2 * t * t : -1 + (4 - 2 * t) * t),
    };
  }
}
