<template>
  <component
    :class="computedClasses"
    :type="type"
    :is="tagName"
    :to="(isNuxtLink) ? url : null"
    :href="(!isNuxtLink) ? url : null"
    :disabled="disabledAttribute"
    :target="targetAttribute"
    v-on="listeners"
    v-ripple>
    <svg-icon
      :class="['icon', { '-no-text': !hasText && !loadingText }]"
      v-if="loading"
      icon="preloader"
      spin/>

    <span
      v-if="!loading && !!$slots.icon"
      :class="['icon', { '-no-text': !hasText }]">
      <slot name="icon"/>
    </span>

    <span class="label">
      <template v-if="loading && loadingText">{{ loadingText }}</template>

      <slot v-else/>
    </span>
  </component>
</template>

<script>
import Ripple from '@/directives/Ripple';
// import url from 'url';

export default {
  name: 'BaseButton',
  props: {
    tagName: {
      type: String,
      default: 'button',
    },
    to: {
      type: [String, Object],
      default: null,
    },
    href: {
      type: [String, Object],
      default: null,
    },
    target: {
      type: String,
      default: null,
    },
    type: {
      type: String,
      default: null,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    fullWidth: {
      type: Boolean,
      default: false,
    },
    iconRight: {
      type: Boolean,
      default: false,
    },
    circle: {
      type: Boolean,
      default: false,
    },
    loading: {
      type: Boolean,
      default: false,
    },
    baseClassName: {
      type: String,
      default: 'button',
    },
    size: {
      type: String,
      default: 'medium',
    },
    variant: {
      type: String,
      default: 'filled',
    },
    color: {
      type: String,
      default: 'primary',
    },
    loadingText: {
      type: String,
      default: 'Загружается',
    },
  },
  computed: {
    computedClasses() {
      return [
        this.baseClassName,
        this.sizeModifier,
        this.colorModifier,
        this.variantModifier,
        this.disabledModifier,
        this.fullWidthModifier,
        this.preloaderModifier,
        this.iconRightModifier,
        this.circleModifier,
        this.hasTextModifier,
      ];
    },
    url() {
      if (this.to !== null) {
        return this.to;
      }

      if (this.href !== null) {
        if (this.href.startsWith('http') || this.href.startsWith('//')) {
          return this.href;
        }

        // TODO url resolver
        return this.href;
        // return url.resolve(`https://www.${this.$store.getters.GET_URL}`, link);
      }

      return null;
    },
    disabledAttribute() {
      return (this.tagName === 'button' && this.disabled);
    },
    targetAttribute() {
      return (this.tagName !== 'button' && this.target) ? this.target : null;
    },
    colorModifier () {
      return (this.color !== '') ? `-${this.color}` : '';
    },
    sizeModifier () {
      return (this.size !== '') ? `-${this.size}` : '';
    },
    variantModifier() {
      return (this.variant !== '') ? `-${this.variant}` : '';
    },
    disabledModifier() {
      return (this.disabled) ? '-disabled' : '';
    },
    fullWidthModifier() {
      return (this.fullWidth) ? '-full-width' : '';
    },
    preloaderModifier() {
      return (this.loading) ? '-preloader' : '';
    },
    iconRightModifier() {
      return (this.iconRight) ? '-icon-right' : '';
    },
    circleModifier() {
      return (this.circle) ? '-circle' : '';
    },
    hasText() {
      return !!this.$slots.default;
    },
    hasTextModifier() {
      return this.hasText && this.circle ? '-has-text' : '';
    },
    isNuxtLink() {
      return this.to !== null;
    },
    listeners() {
      return {
        ...this.$listeners,
        click: (event) => this.$emit('click', event),
      };
    },
  },
  methods: {
    click() {
      if (!this.loading && !this.disabled) {
        this.$emit('click');
      }
    },
  },
  directives: {
    ripple: Ripple,
  },
};
</script>

<style lang="scss" scoped>
$border-radius: 6px;
$font-size: 1rem;
$line-height: 1.25rem;
$font-weight: normal;
$sizes: (
  x-large: (
    padding: (14, 24),
    font-size: 1.125rem,
    line-height: 1.5rem,
    circle-padding: 14px,
  ),
  large: (
    padding: (10, 20),
    font-size: 1.125rem,
    line-height: 1.5rem,
    circle-padding: 10px,
  ),
  small: (
    padding: (8, 16),
    font-size: 0.875rem,
    line-height: 1.25rem,
    circle-padding: 8px,
  ),
  x-small: (
    padding: (8, 16),
    font-size: 0.75rem,
    line-height: 1rem,
    circle-padding: 8px,
  )
);

$colors-default: (
  background: transparent,
  hover-background: transparent,
  focused-background: $gray-40,
  text-color: $gray-80,
);
$colors-filled: (
  primary: (
    background: $red-50,
    hover-background: $red-60,
    focused-background: $red-30,
    text-color: $white,
  ),
  secondary: (
    background: $blue-gray-20,
    hover-background: $blue-gray-30,
    focused-background: $blue-gray-5,
    text-color: $white,
  ),
);
$colors-outlined: (
  primary: (
    outline-color: $red-50,
    background: $white,
    hover-background: $red-10,
    focused-background: $red-20,
    text-color: $red-50,
  ),
  secondary: (
    outline-color: $gray-80,
    background: $white,
    hover-background: $gray-5,
    focused-background: $gray-40,
    text-color: $gray-80,
  ),
);
$colors-shadow: (
  background: $white,
  hover-background: $white,
  focused-background: $gray-30,
  box-shadow: 0 1px 4px rgba($black, 0.25),
  hover-box-shadow: 0 2px 8px rgba($black, 0.25),
  text-color: $gray-80,
);
$colors-link: (
  primary: (
    background: transparent,
    hover-background: transparent,
    focused-background: $gray-40,
    text-color: $gray-90,
    hover-text-color: $red-50,
  ),
  secondary: (
    background: transparent,
    hover-background: $gray-5,
    focused-background: $gray-40,
    text-color: $gray-80,
    hover-text-color: $gray-80,
  ),
);
$colors-disabled: (
  background: $gray-50,
  hover-background: $gray-50,
  focused-background: $gray-50,
  text-color: $gray-60,
);

.button {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  outline: none;
  border: 0;
  cursor: pointer;
  text-decoration: none;
  user-select: none;
  overflow: hidden;
  border-radius: $border-radius;
  font-family: $global-font-family;
  font-weight: $font-weight;
  font-size: $font-size;
  line-height: $line-height;
  padding: 10px 20px;
  background-color: map-get($colors-default, background);
  color: map-get($colors-default, text-color);

  &:hover {
    @include if-hover-enable () {
      background-color: map-get($colors-default, hover-background);
    }
  }

  .label {
    position: relative;
    pointer-events: none;
    display: flex;
    align-items: center;
  }

  &.-full-width {
    width: 100%;
  }

  &.-preloader {
    pointer-events: none;
    cursor: default;
  }

  .icon {
    position: relative;
    flex-shrink: 0;
    display: block;
    margin-right: 8px;
    font-size: 20px;

    &.-no-text {
      margin-right: 0;
      margin-left: 0;
    }
  }

  &.-icon-right .icon {
    order: 1;
    margin-right: 0;
    margin-left: 8px;
  }

  ::v-deep .ripple {
    position: absolute;
    border-radius: 50%;
    width: 0;
    height: 0;
    pointer-events: none;
    transform: scale(0);
    background-color: map-get($colors-default, focused-background);
  }

  &.-filled,
  &.-outlined,
  &.-shadow,
  &.-shadow {
    font-weight: 500;
  }

  &.-filled:not(.-disabled) {
    @each $name, $variant in $colors-filled {
      &.-#{$name} {
        background-color: map-get($variant, background);
        color: map-get($variant, text-color);

        ::v-deep .ripple {
          background-color: map-get($variant, focused-background);
        }

        &:hover {
          @include if-hover-enable () {
            background-color: map-get($variant, hover-background);
          }
        }
      }
    }
  }

  &.-outlined:not(.-disabled) {
    @each $name, $variant in $colors-outlined {
      &.-#{$name} {
        background-color: map-get($variant, background);
        color: map-get($variant, outline-color);
        box-shadow: inset 0 0 0 2px map-get($variant, outline-color);

        ::v-deep .ripple {
          background-color: map-get($variant, focused-background);
        }

        &:hover {
          @include if-hover-enable () {
            background-color: map-get($variant, hover-background);
          }
        }
      }
    }
  }

  &.-shadow:not(.-disabled) {
    background-color: map-get($colors-shadow, background);
    color: map-get($colors-shadow, text-color);
    box-shadow: map-get($colors-shadow, box-shadow);

    ::v-deep .ripple {
      background-color: map-get($colors-shadow, focused-background);
    }

    &:hover {
      @include if-hover-enable () {
        background-color: map-get($colors-shadow, hover-background);
        box-shadow: map-get($colors-shadow, hover-box-shadow);
      }
    }
  }

  &.-link:not(.-disabled) {
    @each $name, $variant in $colors-link {
      &.-#{$name} {
        background-color: map-get($variant, background);
        color: map-get($variant, text-color);

        ::v-deep .ripple {
          background-color: map-get($variant, focused-background);
        }

        &:hover {
          @include if-hover-enable () {
            background-color: map-get($variant, hover-background);
            color: map-get($variant, hover-text-color);
          }
        }
      }
    }
  }

  &.-circle {
    border-radius: 25px;

    &:not(.-has-text) {
      padding: 10px;
    }
  }

  &.-disabled {
    cursor: default;
    background-color: map-get($colors-disabled, background);
    color: map-get($colors-disabled, text-color);

    ::v-deep .ripple {
      background-color: map-get($colors-disabled, focused-background);
    }

    &:hover {
      @include if-hover-enable () {
        background-color: map-get($colors-disabled, hover-background);
      }
    }
  }

  @each $name, $options in $sizes {
    &.-#{$name} {
      padding:
        #{nth(map-get($options, padding), 1)}px
        #{nth(map-get($options, padding), 2)}px;
      font-size: map-get($options, font-size);
      line-height: map-get($options, line-height);

      .icon {
        font-size: map-get($options, line-height);
      }

      &.-circle:not(.-has-text) {
        padding: map-get($options, circle-padding);
      }
    }
  }
}
</style>
