<template>
  <label :class="['radio', {'-checked': isChecked, '-disabled': disabled}]">
    <input
      v-bind="$attrs"
      :disabled="disabled"
      :checked="isChecked"
      :value="value"
      type="radio"
      class="input"
      autocomplete="off"
      @change="$emit('change', value)">

    <span class="radio-wrapper">
      <span class="icon-wrapper">
        <span class="dot"></span>
        <span class="ripple"></span>
      </span>

      <span class="label">
        <slot/>
      </span>
    </span>
  </label>
</template>

<script>
export default {
  name: 'Radio',
  inheritAttrs: false,
  props: {
    value: {},
    modelValue: {},
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  model: {
    prop: 'modelValue',
    event: 'change',
  },
  computed: {
    isChecked() {
      return this.modelValue === this.value;
    },
  },
};
</script>

<style lang="scss" scoped>
.radio{
  position: relative;
  display: block;
  user-select: none;
  font-size: 14px;
  line-height: 20px;

  .input {
    display: none;

    & + .radio-wrapper {
      display: flex;
      max-width: 100%;
      position: relative;
      cursor: pointer;

      .icon-wrapper {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-shrink: 0;
        width: 20px;
        height: 20px;
        box-shadow: inset 0 0 0 2px $gray-60;
        fill: $gray-60;
        border-radius: 50%;
        margin-right: 20px;
        padding: 5px;

        &:last-child {
          margin-right: 0;
        }
      }

      .dot {
        display: block;
        width: 100%;
        height: 100%;
        flex-shrink: 0;
        border-radius: inherit;
        background-color: $gray-60;
        transition: opacity .2s linear;
        visibility: hidden;
        opacity: 0;
      }

      &:hover {
        .dot {
          visibility: visible;
          opacity: 1;
        }
      }
    }

    &:checked + .radio-wrapper {
      .icon-wrapper{
        box-shadow: inset 0 0 0 2px $red-50;
      }

      .dot {
        visibility: visible;
        opacity: 1;
        background-color: $red-50;
      }
    }

    &:disabled + .radio-wrapper {
      cursor: default;

      .icon-wrapper{
        box-shadow: inset 0 0 0 2px $gray-40;
      }

      &, &:hover{
        .dot,
        .ripple {
          display: none;
        }
      }
    }

    &:disabled:checked + .radio-wrapper {
      cursor: default;

      .icon-wrapper{
        box-shadow: inset 0 0 0 2px $gray-40;
      }

      &, &:hover{
        .dot {
          display: block;
          visibility: visible;
          opacity: 1;
          background-color: $gray-40;
        }
      }
    }
  }

  .ripple {
    position: absolute;
    width: 0;
    height: 0;
    display: block;
    background-color: rgba($gray-40, .3);
    border-radius: 50%;
    flex-shrink: 0;
    z-index: -1;
    transition:
      opacity .2s linear,
      width .2s linear,
      height .2s linear,
      background-color .2s linear;
    visibility: hidden;
    opacity: 0;
  }

  &:hover,
  &:focus,
  &:active {
    .ripple {
      visibility: visible;
      opacity: 1;
      width: 40px;
      height: 40px;
    }
  }

  &:focus {
    outline: none;

    .ripple {
      background-color: rgba($gray-40, .5);
    }
  }

  &:active .ripple {
    background-color: rgba($gray-40, .8);
  }

  &.-checked {
    &:hover .ripple {
      background-color: rgba($red-50, .05);
    }

    &:focus .ripple {
      background-color: rgba($gray-40, .8);
    }

    &:active .ripple {
      background-color: rgba($red-50, .2);
    }
  }
}
</style>
