<template>
  <div
    ref="tooltipWrapper"
    class="tooltip-wrapper"
    v-on="listenersMain"
    v-click-outside="clickOutside">

    <slot/>

    <div
      v-show="isActive"
      ref="tooltip"
      :class="[`-${positionX || position}`, computedClasses]"
      :style="style"
      class="tooltip"
      data-tooltip
      v-on="listenersTooltip">
      <a
        href="#"
        v-if="popup"
        class="tooltip-close"
        data-tooltip="close"
        @click.stop.prevent="hideTooltip">
        <svg-icon icon="cross" :size="12"/>
      </a>

      <slot name="content"/>
    </div>
  </div>
</template>

<script>
import { insertBody, removeBody } from '@/utils/helpers';
import ClickOutside from '@/directives/ClickOutside';

export default {
  name: 'Tooltip',
  props: {
    width: {
      type: String,
      default: '320px',
    },
    position: {
      type: String,
      default: 'auto',
    },
    x: {
      type: String,
      default: null,
    },
    y: {
      type: String,
      default: null,
    },
    popup: {
      type: Boolean,
      default: false,
    },
  },
  data: () => ({
    coords: {},
    positionX: null,
    isActive: false,
    hideArrow: false,
  }),
  computed: {
    computedClasses() {
      return [
        this.hasCloseModifier,
        this.hideArrowModifier,
      ];
    },
    hasCloseModifier () {
      return this.popup ? '-has-close' : '';
    },
    hideArrowModifier () {
      return this.hideArrow ? '-hide-arrow' : '';
    },
    style() {
      return {
        left: this.coords.left,
        top: this.coords.top,
        width: this.width,
      };
    },
    listenersMain() {
      return this.getListeners();
    },
    listenersTooltip() {
      return this.getListeners(true);
    },
  },
  methods: {
    getListeners (isTooltip) {
      if (this.popup) {
        return {
          click: (event) => this.toggleTooltip(event, isTooltip),
        };
      }

      return {
        mouseleave: this.hideTooltip,
        mouseenter: this.showTooltip,
      };
    },
    toggleTooltip(event, isTooltip) {
      if (!this.isActive || isTooltip) {
        this.showTooltip();
      } else {
        this.hideTooltip(event);
      }
    },
    showTooltip() {
      const { tooltip } = this.$refs;

      insertBody(tooltip);

      this.isActive = true;

      this.$nextTick(this.changePosition);
    },
    hideTooltip(event) {
      if (event.target.hasAttribute('data-tooltip') && this.popup) {
        return false;
      }

      const { tooltip } = this.$refs;

      this.isActive = false;

      if (tooltip) {
        removeBody(tooltip);
      }
    },
    changePosition() {
      this.positionX = null;
      this.hideArrow = false;

      if (this.x && this.y) {
        this.coords = {
          left: this.x,
          top: this.y,
        };

        this.positionX = 'custom';
        this.hideArrow = true;

        return false;
      }

      const { tooltipWrapper, tooltip } = this.$refs;
      const arrowSize = 12;
      const scrollTopX = window.pageYOffset || document.documentElement.scrollTop;
      const { clientWidth: wrapperWidth, clientHeight: wrapperHeight } = tooltipWrapper;
      const { top: wrapperTop, left: wrapperLeft } = tooltipWrapper.getBoundingClientRect();

      let topX = 0;
      let leftX = 0;

      switch (this.position) {
        case 'bottom':
          topX = wrapperTop + scrollTopX + wrapperHeight + arrowSize;
          leftX = wrapperLeft - (tooltip.clientWidth / 2) + (wrapperWidth / 2);
          break;

        case 'left':
          leftX = wrapperLeft - tooltip.clientWidth - arrowSize;
          topX = wrapperTop + scrollTopX + (wrapperHeight / 2) - (tooltip.clientHeight / 2);
          break;

        case 'right':
          leftX = wrapperLeft + wrapperWidth + arrowSize;
          topX = wrapperTop + scrollTopX + (wrapperHeight / 2) - (tooltip.clientHeight / 2);
          break;

        default:
          topX = wrapperTop + scrollTopX - tooltip.clientHeight - arrowSize;
          leftX = wrapperLeft - (tooltip.clientWidth / 2) + (wrapperWidth / 2);
          this.positionX = 'top';
      }

      // auto position right|left
      if (Math.sign(leftX) === -1 || window.innerWidth - (leftX + tooltip.clientWidth) <= 20) {
        topX = wrapperTop + scrollTopX - tooltip.clientHeight - arrowSize;
        leftX = wrapperLeft;
        this.hideArrow = true;
      }

      // auto position from top to bottom
      if (wrapperTop < (tooltip.clientHeight + arrowSize)) {
        topX = wrapperTop + scrollTopX + wrapperHeight + arrowSize;

        if (this.position === 'right' || this.position === 'left') {
          leftX = wrapperLeft;
          this.hideArrow = true;
        }

        this.positionX = 'bottom';
      } else if ( // auto position from bottom to top
        window.innerHeight - (wrapperTop + wrapperHeight)
        < tooltip.clientHeight + arrowSize
      ) {
        topX = wrapperTop + scrollTopX - tooltip.clientHeight - arrowSize;

        if (this.position === 'right' || this.position === 'left') {
          leftX = wrapperLeft;
          this.hideArrow = true;
        }

        this.positionX = 'top';
      }

      this.coords = {
        left: `${leftX}px`,
        top: `${topX}px`,
      };
    },
    clickOutside(event) {
      if (this.isActive) {
        this.hideTooltip(event);
      }
    },
  },
  directives: {
    'click-outside': ClickOutside,
  },
};
</script>

<style lang="scss" scoped>
.tooltip {
  position: absolute;
  padding: 18px;
  background: $white;
  box-shadow: 0 2px 8px rgba($black, .25);
  max-width: 100%;
  border-radius: 6px;
  z-index: 100;

  &:after {
    content: '';
    display: block;
    position: absolute;
    border: 10px solid transparent;
    border-top-color: $white;
  }

  &-close {
    display: block;
    position: absolute;
    padding: 11px;
    top: 0;
    right: 0;
    color: $gray-70;
    line-height: 1;

    &:hover {
      color: $red-50;
    }
  }

  &.-top:after {
    left: 50%;
    transform: translateX(-50%);
    bottom: -20px;
  }

  &.-bottom:after {
    left: 50%;
    transform: translateX(-50%) scale(-1);
    top: -20px;
  }

  &.-right:after {
    top: 50%;
    transform: translateY(-50%) rotate(90deg);
    left: -20px;
  }

  &.-left:after {
    top: 50%;
    transform: translateY(-50%) rotate(-90deg);
    right: -20px;
  }

  &.-has-close {
    padding-right: 40px;
  }

  &.-hide-arrow:after {
    display: none;
  }

  &.-custom {
    position: fixed;
  }
}
</style>
