<template>
  <label :class="['checkbox', {'-checked': isChecked, '-disabled': disabled}]">
    <input
      v-bind="$attrs"
      v-on="listeners"
      :checked="isChecked"
      :disabled="disabled"
      :value="value"
      type="checkbox"
      class="input"
      autocomplete="off">

    <span class="checkbox-wrapper">
      <span class="icon-wrapper">
        <svg-icon class="icon" icon="check"></svg-icon>
        <span class="ripple"></span>
      </span>

      <span class="label">
        <slot/>
      </span>
    </span>
  </label>
</template>

<script>
export default {
  name: 'Checkbox',
  inheritAttrs: false,
  props: {
    value: {
      type: null,
    },
    modelValue: {
      type: null,
      default: false,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    valueFieldName: {
      type: String,
      default: 'value',
    },
  },
  model: {
    prop: 'modelValue',
    event: 'change',
  },
  computed: {
    isChecked() {
      return this.isModelArray ? this.isArrayIncludeValue : this.modelValue;
    },
    isArrayIncludeValue() {
      if (typeof this.value !== 'object') {
        return this.isModelArray && this.modelValue.indexOf(this.value) !== -1;
      }

      return this.isModelArray && this.modelValue
        .findIndex((item) => item[this.valueFieldName] === this.value[this.valueFieldName]) !== -1;
    },
    isModelArray() {
      return Array.isArray(this.modelValue);
    },
    listeners() {
      return {
        change: () => {
          if (this.isModelArray) {
            this.setArray();
          } else if (['string', 'number'].indexOf(typeof this.value) !== -1) {
            this.setValueString();
          } else {
            this.$emit('change', !this.modelValue);
          }
        },
      };
    },
  },
  methods: {
    setArray() {
      const value = [...this.modelValue];

      if (this.isArrayIncludeValue) {
        value.splice(value.indexOf(this.value), 1);
        this.$emit('change', value);
      } else {
        value.push(this.value);
        this.$emit('change', value);
      }
    },
    setValueString() {
      this.$emit('change', this.modelValue !== this.value ? this.value : null);
    },
  },
};
</script>

<style lang="scss" scoped>
.checkbox{
  position: relative;
  display: block;
  user-select: none;
  font-size: 14px;
  line-height: 20px;

  .input {
    display: none;

    & + .checkbox-wrapper {
      display: flex;
      max-width: 100%;
      position: relative;
      cursor: pointer;

      .icon-wrapper {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-shrink: 0;
        width: 18px;
        height: 18px;
        box-shadow: inset 0 0 0 2px $gray-60;
        color: $gray-60;
        background-color: transparent;
        border-radius: 3px;
        margin-right: 20px;
        padding: 3px;

        &:last-child {
          margin-right: 0;
        }
      }

      .icon {
        display: block;
        width: 100%;
        height: 100%;
        flex-shrink: 0;
        transition: opacity .2s linear;
        visibility: hidden;
        opacity: 0;
      }

      &:hover {
        .icon {
          visibility: visible;
          opacity: 1;
        }
      }
    }

    &:checked + .checkbox-wrapper {
      .icon-wrapper{
        box-shadow: none;
        color: $white;
        background-color: $red-50;
      }

      .icon {
        visibility: visible;
        opacity: 1;
      }
    }

    &:disabled + .checkbox-wrapper {
      cursor: default;

      .icon-wrapper{
        box-shadow: inset 0 0 0 2px $gray-40;
        color: $gray-40;
        background-color: transparent;
      }

      &, &:hover{
        .icon,
        .ripple {
          display: none;
        }
      }
    }

    &:disabled:checked + .checkbox-wrapper {
      cursor: default;

      .icon-wrapper{
        box-shadow: none;
        color: $white;
        background-color: $gray-40;
      }

      &, &:hover{
        .icon {
          display: block;
          visibility: visible;
          opacity: 1;
        }
      }
    }
  }

  .ripple {
    position: absolute;
    width: 0;
    height: 0;
    display: block;
    background-color: rgba($gray-40, .3);
    border-radius: 50%;
    flex-shrink: 0;
    z-index: -1;
    transition:
      opacity .2s linear,
      width .2s linear,
      height .2s linear,
      background-color .2s linear;
    visibility: hidden;
    opacity: 0;
  }

  &:hover,
  &:focus,
  &:active {
    .ripple {
      visibility: visible;
      opacity: 1;
      width: 40px;
      height: 40px;
    }
  }

  &:focus {
    outline: none;

    .ripple {
      background-color: rgba($gray-40, .5);
    }
  }

  &:active .ripple {
    background-color: rgba($gray-40, .8);
  }

  &.-checked {
    &:hover .ripple {
      background-color: rgba($red-50, .05);
    }

    &:focus .ripple {
      background-color: rgba($gray-40, .8);
    }

    &:active .ripple {
      background-color: rgba($red-50, .2);
    }
  }
}
</style>
