<template>
  <div
    :class="{ '-disabled': disabled }"
    class="range-slider"
    @mousewheel.prevent="mousewheelx"
    @keydown.left="keydownLeft"
    @keydown.right="keydownRight">
    <div
      ref="slider"
      class="range-slider-line"
      @click="clickSlider">
      <span
        :style="styleLineOne"
        class="range-slider-line-one"></span>

      <span
        v-for="(tick,index) in countDots"
        :key="index"
        :style="styleDots(index)"
        class="range-slider-dot">
      </span>
    </div>

    <div
      :style="getControlPosition('leftx')"
      :tabindex="disabled ? '-1' : tabindex"
      class="range-slider-control"
      @touchstart="() => controlHandler()"
      @mousedown="() => controlHandler()">
      <span v-if="showLabel" class="range-slider-control-label">
        {{ localValue }}
      </span>
    </div>

    <div
      :style="getEffectPosition('leftx')"
      class="effect"></div>

    <div
      v-if="isRange"
      :style="getControlPosition('leftTwo')"
      class="range-slider-control"
      @touchstart="() => controlHandler(true)"
      @mousedown="() => controlHandler(true)">
    </div>

    <div
      v-if="isRange"
      :style="getEffectPosition('leftTwo')"
      class="effect"></div>
  </div>
</template>

<script>
const MAX_DOTS = 50;

export default {
  name: 'RangeSlider',
  props: {
    double: {
      type: Boolean,
      default: false,
    },
    step: {
      default: 1,
      type: Number,
    },
    max: {
      type: Number,
      default: 100,
    },
    min: {
      type: Number,
      default: 0,
    },
    value: {
      type: null,
    },
    noDelay: {
      type: Boolean,
      default: false,
    },
    hasLabel: {
      type: Boolean,
      default: false,
    },
    hasDots: {
      type: Boolean,
      default: false,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    tabindex: {
      type: String,
      default: '1',
    },
    delay: {
      type: Number,
      default: 100,
    },
  },
  data: () => ({
    leftx: 0,
    leftTwo: 0,
    isTwo: false,
    timer: null,
    localValue: 0,
  }),
  computed: {
    styleLineOne() {
      let widthx = this.leftTwo - this.leftx;
      let { leftx } = this;

      if (this.leftx > this.leftTwo) {
        widthx = this.leftx - this.leftTwo;
        leftx = this.leftTwo;
      }

      return {
        width: `${widthx}%`,
        left: `${leftx}%`,
      };
    },
    isRange() {
      return !Number.isNaN(this.value.min + this.value.max);
    },
    stepDecimals() {
      return this.step % 1 !== 0;
    },
    countDots() {
      const count = Math.round((this.max + 1) / this.step);
      return this.hasDots && count < MAX_DOTS ? Math.round((this.max + 1) / this.step) : 0;
    },
    showLabel() {
      return this.hasLabel && !this.isRange;
    },
  },
  watch: {
    value: {
      immediate: true,
      handler: 'changePosition',
    },
  },
  methods: {
    mousewheelx(evt) {
      if (this.isRange) return;

      if (evt.wheelDelta > 0) {
        let val = this.value + this.step;
        val = this.stepDecimals ? this.toDecimal(val) : Math.round(val);
        if (this.value >= this.max) {
          val = this.max;
        }
        this.$emit('input', val);
      } else {
        let val = this.value - this.step;
        val = this.stepDecimals ? this.toDecimal(val) : Math.round(val);
        if (this.value <= this.min) {
          val = this.min;
        }
        this.$emit('input', val);
      }
    },
    keydownLeft() {
      if (this.isRange) return;

      let val = this.value - this.step;
      val = this.stepDecimals ? this.toDecimal(val) : Math.round(val);
      if (this.value === this.min) {
        val = this.min;
      }
      this.emitInput(val);
    },
    keydownRight() {
      if (this.isRange) return;

      let val = this.value + this.step;
      val = this.stepDecimals ? this.toDecimal(val) : Math.round(val);
      if (this.value >= this.max) {
        val = this.max;
      }
      this.emitInput(val);
    },
    controlHandler(isTwo) {
      this.isTwo = Boolean(isTwo);
      this.activeFocus();
    },
    activeFocus() {
      window.addEventListener('mouseup', this.removeEvents);
      window.addEventListener('mousemove', this.mouseMovex);
      window.addEventListener('touchmove', this.mouseMovex);
      window.addEventListener('touchend', this.removeEvents);
    },
    mouseMovex(evt) {
      const { slider } = this.$refs;
      let leftx;

      if (evt.type === 'touchmove') {
        leftx = evt.targetTouches[0].clientX - slider.getBoundingClientRect().left;
      } else {
        leftx = evt.clientX - slider.getBoundingClientRect().left;
      }

      if (Math.sign(leftx) === -1) {
        leftx = 0;
      } else if (leftx > slider.clientWidth) {
        leftx = slider.clientWidth;
      }

      this.changeLeft(leftx);
    },
    removeEvents() {
      window.removeEventListener('mouseup', this.removeEvents);
      window.removeEventListener('mousemove', this.mouseMovex);
      window.removeEventListener('touchmove', this.mouseMovex);
      window.removeEventListener('touchend', this.removeEvents);
    },
    clickSlider(evt) {
      const { slider } = this.$refs;
      const leftx = evt.clientX - slider.getBoundingClientRect().left;

      const percentX = Math.round((leftx / slider.clientWidth) * 100);

      if (this.isRange) {
        this.isTwo = Math.abs(percentX - this.leftx) > Math.abs(percentX - this.leftTwo);
      }

      this.changeLeft(leftx);
    },
    changeLeft(leftx) {
      const { slider } = this.$refs;
      const percentX = (leftx / slider.clientWidth) * 100;
      const lengthPerStep = 100 / ((this.max - this.min) / this.step);
      const steps = Math.round(percentX / lengthPerStep);
      let val = steps * lengthPerStep * (this.max - this.min) * 0.01 + this.min;
      val = this.stepDecimals ? this.toDecimal(val) : Math.round(val);
      let newVal;

      if (val > this.max) {
        val = this.max;
        this[this.isTwo ? 'leftTwo' : 'leftx'] = 100;
      } else {
        this[this.isTwo ? 'leftTwo' : 'leftx'] = steps * lengthPerStep;
      }

      if (this.isRange) {
        newVal = { ...this.value };
        const min = Math.round((this.leftTwo / 100) * (this.max / this.step)) * this.step;
        const max = Math.round((this.leftx / 100) * (this.max / this.step)) * this.step;
        if (min < max) {
          newVal[this.isTwo ? 'min' : 'max'] = val;
        } else if (min > max) {
          newVal[this.isTwo ? 'max' : 'min'] = val;
        } else {
          newVal = { min: val, max: val };
        }
      } else {
        newVal = val;
        this.localValue = val;
      }

      this.emitInput(newVal);
    },
    emitInput(val) {
      if (this.noDelay) {
        this.$emit('input', val);
      } else {
        clearTimeout(this.timer);
        this.timer = setTimeout(() => this.$emit('input', val), this.delay);
      }
    },
    changePosition(value) {
      if (value > this.max) return;

      this.localValue = value;

      if (this.isRange) {
        this.leftx = ((this.value.max - this.min) / (this.max - this.min)) * 100;
        this.leftTwo = ((this.value.min - this.min) / (this.max - this.min)) * 100;
      } else {
        this.leftx = ((this.value - this.min) / (this.max - this.min)) * 100;
      }
    },
    toDecimal(value) {
      return parseFloat(value.toFixed(1));
    },
    styleDots(index) {
      const lengthPerStep = 100 / ((this.max - this.min) / this.step);
      const left = index * lengthPerStep;
      return {
        left: `${left}%`,
        display: left > 100 ? 'none' : 'inline-block',
      };
    },
    getControlPosition(field) {
      return { left: `calc(${this[field]}% - 6px)` };
    },
    getEffectPosition(field) {
      return { left: `calc(${this[field]}% - 20px)` };
    },
  },
};
</script>

<style lang="scss" scoped>
.range-slider {
  position: relative;
  width: 200px;
  display: flex;
  align-items: center;
  user-select: none;
  padding: 6px 0;

  &-line {
    height: 2px;
    background-color: $red-20;
    position: absolute;
    width: 100%;

    &-one {
      width: 20px;
      height: 100%;
      top: 0px;
      position: absolute;
      left: 0px;
      z-index: 1;
      background-color: $red-50;
    }
  }

  &-dot {
    position: absolute;
    left: 100px;
    background:$red-30;
    width: 2px;
    height: 100%;
    top: 0px;
    z-index: 10;
  }

  .effect {
    position: absolute;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: $red-20;
    opacity: 0;
    transition: opacity .2s;
  }

  &-control {
    position: absolute;
    z-index: 10;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-color: $red-50;
    border: none;
    cursor: pointer;
    outline: none;

    &-label {
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 12px;
      line-height: 1;
      color: $white;
      background: $red-50;
      border-radius: 4px;
      padding: 4px 6px;
      margin-bottom: 4px;
      position: absolute;
      bottom: 100%;
      left: 50%;
      transform: translateX(-50%);

      &:before {
        content: '';
        width: 0;
        height: 0;
        border: 3px solid transparent;
        border-top-color: $red-50;
        position: absolute;
        top: 100%;
      }
    }

    &:hover + .effect {
      opacity: .5;
    }

    &:focus + .effect {
      opacity: .7;
    }

    &:active {
      & + .effect {
        opacity: 1;
      }

      .range-slider-control-label {
        display: flex;
      }
    }
  }

  &.-disabled {
    pointer-events: none;

    .range-slider-line {
      background-color: $gray-20;
    }

    .range-slider-line-one {
      background-color: $gray-50;
    }

    .range-slider-dot {
      background-color: $gray-40;
    }

    .range-slider-control {
      background-color: $gray-50;
    }

    .effect {
      width: 20px;
      height: 20px;
      background-color: $gray-20;
      opacity: 1;
      margin-left: 10px;
    }
  }
}
</style>
